<template>
  <ui-card class="orgTree">
    <div slot="title" style="height:24px;">
      <ui-icon type="levels"></ui-icon>
      <span>部门层级</span>
    </div>
    <div class="cardBody" :style="cardBody">
      <ui-tree
          :data="data"
          :render="renderTree"
          @on-select-change="handleSelectChange"></ui-tree>
    </div>
  </ui-card>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'orgList',
    props: {
      height: '',
      operFlag: Boolean,
      data: Array,
      selData: Object
    },
    computed: {
      cardBody () {
        return {
          width: '100%',
          height: `${this.height - 164}px`,
          overflow: 'auto',
          padding: '16px'
        };
      }
    },
    methods: {
      /**
       * 绘制机构树
       * */
      renderTree (h, {root, node, data}) {
        return h('div',
          {
            'class': {
              treeListClass: true,
              treeClickClass: this.selData === data
            },
            on: {
              click: () => {
                this.handleSelectChange([data]);
              }
            }
          },
          [
            h('uiIcon',
              {
                props: {
                  type: 'ios-folder-outline'
                },
                style: {
                  marginRight: '8px'
                }
              }, []
            ),
            h('span', data.title ? data.title : data.dept_name + '-' + data.deptno),
            h('uiButtonGroup',
              {
                'class': {
                  btnClass: true,
                  setOrgBtnClass: !this.operFlag
                },
                props: {
                  size: 'small'
                }
              }, [
                h('uiButton',
                  {
                    attrs: {
                      title: '新增部门信息'
                    },
                    props: {
                      type: 'text',
                      icon: 'plus',
                      disabled: this.selData !== data
                    },
                    on: {
                      click: () => {
                        this.$emit('on-click', 'A');
                      }
                    }
                  }
                ),
                h('uiButton', {
                  attrs: {
                    title: '修改部门信息'
                  },
                  props: {
                    type: 'text',
                    icon: 'edit',
                    disabled: this.selData !== data
                  },
                  on: {
                    click: () => {
                      this.$emit('on-click', 'U');
                    }
                  }
                }),
                h('uiButton',
                  {
                    attrs: {
                      title: '删除部门信息'
                    },
                    props: {
                      type: 'text',
                      icon: 'trash-a',
                      disabled: this.selData !== data
                    },
                    on: {
                      click: () => {
                        this.$emit('on-click', 'D');
                      }
                    }
                  }
                )
              ]
            )
          ]
        );
      },
      renderBtn () {
        let renderBtns = [];
        for (let i = 0, btn; btn = this.btns[i]; i++) {
          let tmp = `h('uiButton',
            {
              attrs: {
                title: ` + btn.title + `
              },
              props: {
                type: ` + btn.text + `,
                icon: ` + btn.icon + `,
                disabled: ` + btn.disabled + `
              },
              on: {
                click: () => {
                  this.$emit('on-click', ` + btn.flag + `);
                }
              }
            }
          )`;
          renderBtns.push(tmp);
        }
        return renderBtns;
      },
      /**
       * 机构树数据选中事件
       * @param data 选中的数组
       * */
      handleSelectChange (data) {
        this.$emit('on-select-change', data);
      }
    },
    mounted () {
      if (this.data.length > 0) {
        this.handleSelectChange(this.data);
      }
    },
    watch: {
      data (newVal, oldVal) {
        if (newVal && newVal.length > 0 && newVal !== oldVal) {
          this.handleSelectChange(newVal);
        }
      }
    }
  };
</script>

<style lang="less" scoped>
  .orgTree {
    /deep/ .treeListClass {
      position: relative;
      display: inline-block;
      width: 100%;
      &:hover {
        cursor: pointer;
      }
    }
    .ivu-tree {
      width: 100%;
    }
    /deep/ .ivu-tree-arrow {
      cursor: pointer;
      width: 12px;
      text-align: center;
      display: inline-block;
    }
    /deep/ .btnClass {
      position: absolute;
      right: 0px;
    }
    /deep/ .setOrgBtnClass {
      display: none;
    }
    /deep/ .treeClickClass {
      background-color: #ebf7ff;
    }
    /deep/ .ivu-input-wrapper {
      margin-left: 10px;
    }
  }

  .ivu-card {
    height: 100%;
    /deep/ .ivu-card-body {
      padding: 0;
    }
    /deep/ .ivu-card-head {
      border-bottom: 1px solid #e6e6e6;
      padding: 8px;
      line-height: 1;
    }
  }
</style>



