<template>
  <ui-card class="orgTree">
    <div slot="title">
      <ui-row>
        <ui-col span="6">
          <p>机构列表</p>
        </ui-col>
        <ui-col span="18">
          <ui-input type="text" icon='search' size="small"
                    placeholder="请输入机构关键字" @on-change="searchInputChange"></ui-input>
        </ui-col>
      </ui-row>
    </div>
    <div class="cardBody" :style="cardBodyStyle">
      <ui-tree :data="treeList"
               :render="drawTree"
               @on-select-change="selectTreeData">
      </ui-tree>
    </div>
  </ui-card>
</template>
<script type="text/ecmascript-6">
  export default{
    props: {
      cardHeight: '',
      isSetOrg: Boolean,
      isQueryTree: Boolean,
      treeList: Array,
      treeSelectData: Object
    },
    methods: {
      /**
       * 绘制机构树
       * */
      drawTree (h, {root, node, data}) {
        return h('div',
          {
            'class': {
              treeListClass: true,
              treeClickClass: this.treeSelectData === data
            },
            on: {
              click: ()=> {
                this.selectTreeData([data]);
              }
            }
          }, [
            h('uiIcon',
              {
                props: {
                  type: 'ios-folder-outline'
                },
                style: {
                  marginRight: '8px'
                }
              }, []
            ),
            h('span', data.title ? data.title : data.orgname + '-' + data.orgid),
            h('uiButtonGroup',
              {
                'class': {
                  btnClass: true,
                  setOrgBtnClass: !this.isSetOrg
                },
                props: {
                  size: 'small'
                }
              }, [
                h('uiButton',
                  {
                    attrs: {
                      title: '增加子机构'
                    },
                    props: {
                      type: 'text',
                      icon: 'plus',
                      disabled: this.treeSelectData !== data || this.isQueryTree
                    },
                    on: {
                      click: ()=> {
                        if (this.isSetOrg) {
                          this.$emit('on-addTree');
                          event.stopPropagation(); // 阻止向上冒泡
                        }
                      }
                    }
                  }, []
                ),
                h('uiButton',
                  {
                    attrs: {
                      title: '删除机构'
                    },
                    props: {
                      type: 'text',
                      icon: 'trash-a',
                      disabled: this.treeSelectData !== data || this.isQueryTree
                    },
                    on: {
                      click: ()=> {
                        if (this.isSetOrg) {
                          this.treeSelectData = data;
                          this.$emit('on-delTree');
                          event.stopPropagation(); // 阻止向上冒泡
                        }
                      }
                    }
                  }, []
                )
              ]
            )
          ]
        );
      },

      /**
       * 机构树数据选中事件
       * @param data 选中的数组
       * */
      selectTreeData (data) {
        this.$emit('on-selectTreeData', data);
      },

      searchInputChange (event) {
        this.$emit('on-searchInputChange', event);
      }
    },
    computed: {
      cardBodyStyle () {
        return {
          width: '100%',
          height: `${this.cardHeight - 194.6}px`,
          overflow: 'auto',
          padding: '16px'
        };
      }
    },
    watch: {
      treeList (newVal, oldVal) {
        if (newVal.length > 0 && newVal !== oldVal) {
          this.selectTreeData(newVal);
        }
      }
    },
    created () {
      if (this.treeList.length > 0) {
        this.selectTreeData(this.treeList);
      }
    }
  };
</script>
<style lang="less" scoped>
  .orgTree {
    .treeListClass {
      display: inline-block;
      &:hover {
        cursor: pointer;
      }
    }
    .ivu-tree {
      width: 100%;
    }
    /deep/ .ivu-tree-arrow {
      cursor: pointer;
      width: 12px;
      text-align: center;
      display: inline-block;
    }
    /deep/ .btnClass {
      position: absolute;
      right: 0px;
    }
    /deep/ .setOrgBtnClass {
      display: none;
    }
    /deep/ .treeListClass {
      display: inline-block;
      width: 100%;
      &:hover {
        cursor: pointer;
      }
    }
    /deep/ .treeClickClass {
      background-color: #ebf7ff;
    }
    /deep/ .ivu-input-wrapper {
      margin-left: 10px;
    }
  }
</style>



