<template>
  <div class="ui-SplitLine" :style="style">
    <fieldset class="ui-SplitLine-field" @click="clickMore">
      <legend>
        <span class="ui-SplitLine-field-span">{{content}}</span>
        <ui-icon :type="icon" :color="color"></ui-icon>
      </legend>
    </fieldset>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'uiSplitLine',
    props: {
      icon: { // 图标
        type: String,
        default: 'double-arrow-down'
      },
      color: { // 字体与图标颜色
        type: String,
        default: '#19be6b'
      },
      width: { // 虚线长度
        type: Number,
        default: 300
      },
      content: { // 显示文字
        type: String,
        default: '更多设置'
      }
    },
    methods: {
      /**
       * 点击事件
       * */
      clickMore () {
        this.$emit('on-clickmore');
      }
    },
    computed: {
      style () {
        return {
          color: this.color,
          width: `${this.width}px`
        };
      }
    }
  };
</script>

<style lang="less" scoped>
  .ui-SplitLine {
    text-align: center;
    margin: 0 auto;
    cursor: pointer;
    /*font-weight: bold;*/
    &-field {
      border: none;
      border-top: 1px dashed #d0cdc7;
      padding-bottom: 30px;
      legend {
        padding: 0 10px;
      }
      &-span {
        margin-right: 5px;
      }
    }
  }
</style>


