import {copyObject, cnvToTree, packet} from '../../assets/js/utils.js';

/*
 * 处理用户、角色执行权限、授权权限数据
 * @param vm vue 实例对象
 * @param rightType：标识执行权限、授权权限
 * @param record：接口返回数据
 * */
export function dealRightData(vm, rightType, record) {
  let haveRightData = [];
  let data = copyObject(record);
  let flag = 'ischoose';
  for (let i = 0; i < data.length; i++) {
    vm.$set(data[i], 'title', `${data[i].description}-${data[i].menuid}`);
    if (data[i][flag] === '1') {
      haveRightData.push(data[i]);
      data[i]['checked'] = true;
      /*   if (data[i][flag] === '1') {
       data[i]['checked'] = true;
       vm.$set(data[i], 'color', '#ff9900');
       } else {
       data[i]['checked'] = true;
       vm.$set(data[i], 'color', '#19be6b');
       }*/
    }
    vm.$set(data[i], 'parentmenuid', data[i].menuposition.substring(0, data[i].menuposition.length - 1));
  }
  return {list: cnvToTree(data, '', 'parentmenuid', 'menuposition'), data: haveRightData};
}

/*
 * 处理用户、角色机构访问权限数据
 * @param record：接口返回数据
 * @param orgList 缓存中机构信息
 * */
export function dealOrgData(vm, record, orgList) {
  let data = record;
  let orgList1 = copyObject(orgList);
  let record1 = [];
  let record2 = [];
  let orgRightList = [];
  for (let i = 0; i < data.length; i++) { // 用户已有的机构权限
    let orgid = (data[i].orgid_many) ? (data[i].orgid_many.indexOf(',') > -1
      ? data[i].orgid_many.split(',') : [data[i].orgid_many]) : [];
    if (data[i].rightflag === '1') {
      record1 = record1.concat(orgid); // 将orgid添加到d中，已经拥有的机构权限,用户权限
    } else {
      record2 = record2.concat(orgid); // 将orgid添加到d中，已经拥有的机构权限，角色权限
    }
  }
  if (record1.length === 0 && record2.length === 0) {
    orgRightList = copyObject(orgList);
  } else {
    if (record1.length !== 0) {
      for (let x = 0; x < record1.length; x++) {
        for (let y = 0; y < orgList1.length; y++) {
          if (orgList1[y].orgid === record1[x]) {
            vm.$set(orgList1[y], 'checked', true);
            vm.$set(orgList1[y], 'color', '#ff9900');
          }
        }
      }
    }
    if (record2.length !== 0) {
      for (let m = 0; m < record2.length; m++) {
        for (let n = 0; n < orgList1.length; n++) {
          if (orgList1[n].orgid === record2[m]) {
            vm.$set(orgList1[n], 'checked', true);
            vm.$set(orgList1[n], 'color', '#19be6b');
          }
        }
      }
    }
    orgRightList = orgList1;
  }
  for (let p = 0; p < orgRightList.length; p++) {
    vm.$set(orgRightList[p], 'title', `${orgRightList[p].orgname}-${orgRightList[p].orgid}`);
  }

  return treeDataStandard(orgRightList, '', 'parentorgid', 'orgid');
}

/*
 * 处理消息权限
 * @param vm vue实例
 * @param record 后台返回数据
 * */
export function dealMsgData(vm, record) {
  let data = copyObject(record);
  for (let i = 0; i < data.length; i++) {
    vm.$set(data[i], 'title', `${data[i].msgname}-${data[i].msgid}`);
    vm.$set(data[i], 'disabled', true);
    if (data[i]['rightflag'] !== '0') {
      vm.$set(data[i], 'checked', true);
      if (data[i]['rightflag'] === '1') {
        vm.$set(data[i], 'color', '#ff9900');
      } else {
        vm.$set(data[i], 'color', '#19be6b');
      }
    }
  }
  return data;
}

/**
 * 获取执行权限、授权权限参数
 * @param vm
 * @param oldCheckData
 * @param data
 * @returns {*}
 */
export function getParam (vm, oldCheckData, data) {
  let menurights = '';
  oldCheckData.forEach((item1) => {
    let flag = data.find((item2) => {
      return item2.menuid === item1.menuid;
    });
    if (!flag) {
      menurights = (menurights.length === 0) ? (item1.menuid + ',D') : (menurights + '|' + item1.menuid + ',D');
    }
  });
  data.forEach((item2) => {
    let flag = oldCheckData.find((item1) => {
      return item1.menuid === item2.menuid;
    });
    if (!flag) {
      menurights = (menurights.length === 0) ? (item2.menuid + ',A') : (menurights + '|' + item2.menuid + ',A');
    }
  });
  if (menurights === '') {
    vm.$uiNotice.warning({
      title: '提醒',
      desc: '权限设置没有改变！ '
    });
    return '';
  } else {
    let pack = packet({
      paramName: 'menurights1',
      paramValue: menurights
    });
    if (vm.userRowData) {
      pack.userid = vm.userRowData.userid;
    } else {
      pack.menuRoleId = vm.roleRowData.menuRoleId;
    }
    return pack;
  }
}

/**
 * 获取下拉框配字典时需要的数据
 * @param dictItem 字典号
 * @param condition 过滤条件
 */
export function initDictData(vm, dictItem, condition) {
  let dictCondition = `e.dictitem==${dictItem}&&e.subitem.trim()!==''`;
  if (condition) dictCondition += `&&${condition}`;
  let dictData = vm.$cache.getCache('dict', dictCondition);
  dictData.map((item) => {
    item.value = item.subitem;
    item.label = item.subitemname;
  });
  return dictData;
}

/**
 * 格式化时间
 * @param date 标准中国时间 必传参数
 * @param format 格式化规则 date:yyyyMMdd time:hhMMss fullData:yyyyMMdd hhMMss 必传参数
 * @param dateSplit 日期分隔符
 * @param timeSplit 时间分隔符
 */
export function formatDate(date, format, dateSplit, timeSplit) {
  let dateValue;
  if (format === 'date' || format === 'fullData') {
    if (dateSplit === undefined) {
      dateSplit = '-';
    }
    dateValue = date.getFullYear() + dateSplit + ('0' + (date.getMonth() + 1)).slice(-2) + dateSplit +
      ('0' + date.getDate()).slice(-2);
  }
  if (format === 'fullData') {
    dateValue += ' ';
  }
  if (format === 'time' || format === 'fullData') {
    if (timeSplit === undefined) {
      timeSplit = ':';
    }
    dateValue += ('0' + (date.getHours() + 1)).slice(-2) + timeSplit + ('0' + (date.getMinutes() + 1)).slice(-2) +
      timeSplit + ('0' + (date.getSeconds() + 1)).slice(-2);
  }
  return dateValue;
}



