<template>
  <div class="page">
    <div class="page-panel" :style="styles">
      <div class="orgMgt">
        <ui-row>
          <ui-col span="5">
            <org-list
              :height="height"
              :operFlag="false"
              :searchFlag="searchFlag"
              :data="orgData"
              :selData="selData"
              @on-click="handleSetting"
              @on-select-change="handleSelectChange"></org-list>
          </ui-col>
          <ui-col span="19" style="padding-left: 10px">
            <org-table
              :height="height"
              :disabled="disabled"
              :columns="columns"
              :orgList="orgList"
              @on-setting="handleSetting"
              @on-row-click="handleRowClick"></org-table>
          </ui-col>
        </ui-row>
        <common-dialog
          :visible="isShow"
          :title="title"
          :height="moduleHeight"
          @on-submit="handleSubmit"
          @on-cancel="handleCancel">
          <div slot="content" style="margin: 20px">
            <org-form ref="orgform"
                      :disabled="operFlag"
                      :orgMsgForm="orgMsgForm"
                      :deptData="sourceData">
            </org-form>
          </div>
        </common-dialog>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

  import orgList from '../orgList.vue';
  import orgTable from './orgTable.vue';
  import orgForm from './orgForm.vue';
  import commonDialog from '../../commons/commonDialog';
  import {copyObject, cnvToTree} from '@/assets/js/utils';

  export default {
    name: 'orgMgt-page',
    components: {
      orgList,
      orgTable,
      orgForm,
      commonDialog
    },
    data () {
      return {
        height: document.body.clientHeight,
        operFlag: true,
        searchFlag: false,
        orgData: [],
        selData: {},
        columns: [
          {title: '部门代码', key: 'deptno', width: 100},
          {title: '部门名称', key: 'dept_name', width: 200},
          {title: '部门简称', key: 'dept_abbr_name', width: 120},
          {title: '上级部门代码', key: 'up_deptno', width: 150},
          {title: '数据状态', key: 'data_state', width: 100},
          {title: '创建人', key: 'fcu', width: 100},
          {title: '创建时间', key: 'fcd', width: 200},
          {title: '修改人', key: 'lcu', width: 150},
          {title: '修改时间', key: 'lcd', width: 200},
          {title: '层级', key: 'up_deptno', width: 100},
          {title: '有效标识', key: 'enabled_flag', width: 100},
          {title: '专业公司代码', key: 'top_deptno', width: 150},
          {title: 'PS系统唯一键', key: 'ps_uni_deptid', width: 150},
          {title: 'PS专业公司代码', key: 'ps_top_deptid', width: 200}
        ],
        sourceData: [],
        orgList: [],
        isShow: false,
        orgMsgForm: {
          dept_name: '',
          deptno: '',
          up_deptno: '',
          dept_abbr_name: '',
          arrangement: '',
          data_state: '',
          enabled_flag: '',
          top_deptno: '',
          ps_uni_deptid: '',
          ps_top_deptid: ''
        },
        searchKey: '',
        disabled: true,
        title: '设置',
        action: '',
        moduleHeight: 425,
        formObj: {}
      };
    },
    methods: {
      resize () {
        this.height = Number(document.documentElement.clientHeight);
        window.onresize = () => {
          this.height = Number(document.documentElement.clientHeight);
        };
      },
      /**
       * 初始化机构信息
       */
      getOrgList () {
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101207',
            param: {}
          },
          success: () => {
            let res = this.$store.getters.jsonData;
            if (res.flag === '0') {
              let record = res.results[0].record;
              this.sourceData = record;
              this.orgData = cnvToTree(copyObject(record), '0', 'up_deptno', 'deptno');
              this.$set(this.orgData[0], 'expand', true);
            } else {
              this.$uiNotice.error({
                title: '查询部门信息失败',
                desc: res.prompt
              });
            }
          }
        });
      },
      setOrgInfo (param) {
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: param.obj_code,
            param: param
          },
          success: () => {
            let res = this.$store.getters.jsonData;
            if (res.flag === '0') {
              this.$uiNotice.info({
                title: param.tip + '部门信息成功',
                desc: res.prompt
              });
              this.getOrgList();
              this.isShow = false;
            } else {
              this.$uiNotice.error({
                title: param.tip + '部门信息失败',
                desc: res.prompt
              });
            }
          }
        });
      },
      handleSelectChange (orgList) {
        this.disabled = true;
        if (orgList && orgList.length === 0) return;
        let list = [orgList[0]];
        this.selData = orgList[0];
        let recuFun = (obj) => {
          for (let i = 0, data; data = this.sourceData[i]; i++) {
            if (obj.deptno === data.up_deptno) {
              list.push(data);
              recuFun(data);
            }
          }
        };
        recuFun(orgList[0]);
        this.orgList = list;
      },
      handleSubmit () {
        this.$refs['orgform'].$refs['orgForm'].validate((valid) => {
          if (valid) {
            let param = {};
            if (this.action === 'A') {
              param.obj_code = '101202';
              param.tip = '新增';
            } else if (this.action === 'U') {
              param.obj_code = '101203';
              param.tip = '修改';
            }
            param = Object.assign(param, this.orgMsgForm);
            this.setOrgInfo(param);
          }
        });
      },
      handleCancel () {
        this.$refs.orgform.$refs.orgForm.resetFields();
        this.isShow = false;
      },
      handleRowClick (data, index) {
        let obj = copyObject(data);
        this.disabled = false;
        delete obj.children;
        this.formObj = obj;
      },
      handleSetting (action) {
        if (this.formObj.up_deptno === '0' && action === 'U') {
          this.disabled = true;
          this.$uiMessage.info({
            content: '顶层部门不允许修改'
          });
          return;
        }
        this.action = action;
        this.isShow = true;
        if (action === 'A') {
          this.title = '新增部门';
          this.operFlag = false;
          this.orgMsgForm = {};
        } else if (action === 'U') {
          this.title = '修改部门';
          this.operFlag = true;
          this.orgMsgForm = copyObject(this.formObj);
        } else {
          this.isShow = false;
          if (this.formObj.up_deptno === '0') {
            this.$uiMessage.info({
              content: '顶层部门不允许删除'
            });
            return '';
          }
          this.$uiModal.confirm({
            title: '删除',
            content: '确定删除该部门？',
            onOk: () => {
              let param = {};
              param.action = 'D';
              param.obj_code = '101204';
              param.tip = '删除';
              param = Object.assign(param, this.formObj);
              this.setOrgInfo(param);
            }
          });
        }
      }
    },
    computed: {
      styles () {
        return {
          height: `${this.size - 101}px`
        };
      }
    },
    watch: {
      action (action) {
        this.action = action;
      }
    },
    mounted () {
      this.resize();
      this.getOrgList();
    }
  };
</script>

<style lang="less" scoped>
  .orgMgt {
    width: 100%;
    height: 100%;
  }

  .roleManagement-table {
    /deep/ .ivu-card-head {
      border-bottom: 1px solid #e6e6e6;
      padding: 8px;
      line-height: 1;
    }
  }
</style>



