<template>
  <div @mouseup="mouseup" @mousemove="mousemove">
    <div class="common-dialog-mask" v-show="visible"></div>
    <ui-card class="common-dialog-style" :style="dialogStyle" v-show="visible" ref="card"
             @mousewheel.native="mousewheel">
      <slot name="header">
        <p slot="title" v-if="hasTitle" class="title" @mousedown="mousedown">
          <ui-icon :type="titleIcon"></ui-icon>
          {{title}}
          <ui-icon type="close-round" class='close-button-style' @click.native="handleDialogCancel"
                   v-if="allowClose"></ui-icon>
        </p>
      </slot>
      <div :style="contentStyle" :id="contentId">
        <slot name="content"></slot>
      </div>
      <div :class="['button-style', {'hide-button-tool':hasButton===false}]">
        <slot name="buttons">
          <div v-if="hasButton">
            <ui-button type="primary" size="small" icon="checkmark-round" style="margin-right:13px;"
                       @click="handleDialogSubmit">确定
            </ui-button>
            <div class="split-style"></div>
            <ui-button type="text" size="small" @click="handleDialogCancel">取消</ui-button>
          </div>
        </slot>
      </div>
    </ui-card>
  </div>
</template>
<script>
  export default {
    name: 'CommonDialog',
    props: {
      height: {
        type: Number,
        default: 0
      },
      width: {
        type: Number,
        default: 0
      },
      title: {
        type: String,
        default: '页面对话框'
      },
      left: {
        type: Number,
        default: 0
      },
      top: {
        type: Number,
        default: 0
      },
      visible: {
        type: Boolean,
        default: false
      },
      hasTitle: {
        type: Boolean,
        default: true
      },
      titleIcon: {
        type: String,
        default: 'chatbox'
      },
      contentId: {
        type: String
      },
      hasButton: {
        type: Boolean,
        default: true
      },
      allowClose: {
        type: Boolean,
        default: true
      }
    },
    data () {
      return {
        isdown: false,
        positionX: 0,
        positionY: 0,
        sx: 0,
        sy: 0,
        computedStyle: {}
      };
    },
    computed: {
      dialogStyle () {
        let style = {};
        if (this.height === 0 && this.width === 0) {
          style.height = '240px';
          style.width = '400px';
        } else if (this.height !== 0 && this.width !== 0) {
          style.height = `${this.height}px`;
          style.width = `${this.width}px`;
        } else if (this.height !== 0 && this.width === 0) {
          style.height = `${this.height}px`;
          style.width = `${16 / 9 * this.height}px`;
        } else {
          style.height = `${9 / 16 * this.width}px`;
          style.width = `${this.width}px`;
        }
        if (this.top !== 0) {
          style.top = `${this.top}px`;
        } else {
          style.top = `${100 + 0.1 * Number(Number(document.documentElement.clientHeight) - 100)}px`;
        }
        let height = Number(style.height.substring(0, style.height.length - 2));
        let width = Number(style.width.substring(0, style.width.length - 2));
        if (this.left !== 0) {
          style.left = `${this.left}px`;
        } else {
          style.left = `${0.5 * Number(document.documentElement.clientWidth) - 0.5 * width}px`;
        }
        this.computedStyle.height = height;
        this.computedStyle.width = width;
        return style;
      },
      contentStyle () {
        // 56为按钮区高度 + card的padding
        let contentHeight = this.computedStyle.height - 56;
        if (this.hasTitle) {
          contentHeight -= 39;
        }
        if (!this.hasButton) {
          contentHeight += 40;
        }
        return {
          'height': `${contentHeight}px`,
          'overflow': 'auto'
        };
      }
    },
    methods: {
      /**
       * 鼠标按下触发事件
       * @param event
       * */
      mousedown (event) {
        let that = this;
        let obj = this.$refs['card'].$el;
        this.positionX = event.clientX - parseInt(obj.style.left);
        this.positionY = event.clientY - parseInt(obj.style.top);
        if (!this.isdown) {
          this.isdown = true;
        }
        document.ondragstart = function (event) {
          event.preventDefault();
        };
        document.ondragend = function (event) {
          event.preventDefault();
        };
      },

      /**
       * 鼠标移动事件
       * @param event
       * */
      mousemove (event) {
        let obj = this.$refs['card'].$el;
        if (this.isdown) {
          let x = event.clientX - this.positionX;
          let y = event.clientY - this.positionY;
          obj.style.top = `${event.clientY - this.positionY }px`;
          obj.style.left = `${event.clientX - this.positionX}px`;
        }
      },

      /**
       * 鼠标松开
       * */
      mouseup () {
        if (this.isdown) {
          this.isdown = false;
        }
      },

      /*
       * 鼠标滚动事件
       * @param event
       * */
      mousewheel (event) {
        this.$emit('on-mousewheel', event)
      },

      handleDialogSubmit () {
        this.$emit('on-submit');
      },
      handleDialogCancel () {
        this.$emit('on-cancel');
      }
    },
    watch: {
      visible (val) {
        if (val) {
          this.$emit('on-show');
        } else {
          this.$emit('on-hide');
        }
      }
    },
    mounted () {
      let that = this;
      /* document.onmousemove = function (event) {
       let obj = that.$refs['card'].$el;
       if (that.isdown) {
       obj.style.top = `${event.clientY - that.positionY}px `;
       obj.style.left = `${event.clientX - that.positionX}px`;
       }
       };*/


    }
  };
</script>
<style type="less" scoped>
  /deep/ .ivu-card-head {
    border-bottom: 1px solid #e6e6e6;
    padding: 8px;
    line-height: 1;
    &:hover {
      cursor: pointer;
    }
  }

  /deep/ .ivu-card-body {
    padding: 0;
  }

  .common-dialog-mask {
    position: fixed;
    top: 110px;
    bottom: 0;
    left: 10px;
    right: 10px;
    background-color: rgba(233, 243, 248, 0.4);
    height: 100%;
    z-index: 1000;
  }

  .common-dialog-style {
    position: fixed;
    z-index: 1000;
    animation: dialogShow 0.2s linear;
    &:hover {
      cursor: pointer;
    }
    .title {

    }
  }

  ::-webkit-scrollbar {
    width: 4px;
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: #e6e6e6;
  }

  ::-webkit-scrollbar-track {
    background-color: #fff;
  }

  .button-style {
    text-align: center;
    height: 40px;
    border-top: 1px solid #e6e6e6;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-top: 4px;
    margin-bottom: 4px;
  }

  @keyframes dialogShow {
    0% {
      left: 0%;
    }
  }

  .hide-button-tool {
    display: none;
  }

  .close-button-style {
    float: right;
    cursor: pointer;
    margin-right: 2px;
  }

  .close-button-style:hover {
    color: #2b85e4;
  }
</style>



