<template>
  <ui-form :label-width="120" :model="orgMsgForm" ref="orgForm" :rules="validate">
    <ui-row>
      <ui-col span="12">
        <ui-form-item prop="deptno" label="部门代码">
          <ui-input v-model="orgMsgForm.deptno"
                    type="text"
                    placeholder="部门代码"
                    autocomplete="on"
                    class="labelClass"
                    :maxlength="16"
                    :disabled="disabled"></ui-input>
        </ui-form-item>
      </ui-col>
      <ui-col span="12">
        <ui-form-item prop="dept_name" label="部门名称">
          <ui-input v-model="orgMsgForm.dept_name"
                    type="text"
                    clearable
                    placeholder="部门名称"
                    class="labelClass"></ui-input>
        </ui-form-item>
      </ui-col>
    </ui-row>
    <ui-row>
      <ui-col span="12">
        <ui-form-item prop="dept_abbr_name" label="部门简称">
          <ui-input v-model="orgMsgForm.dept_abbr_name"
                    type="text"
                    clearable
                    placeholder="部门简称"
                    class="labelClass"></ui-input>
        </ui-form-item>
      </ui-col>
      <ui-col span="12">
        <ui-form-item prop="up_deptno" label="上级部门">
          <ui-select v-model="orgMsgForm.up_deptno"
                     clearable
                     filterable
                     placeholder="请选择上级部门"
                     class="labelClass">
            <ui-option v-for="item in deptData"
                       :value="item.deptno"
                       :key="deptData.deptno"
                       :label="item.dept_name">
              <span>{{item.dept_name}}</span>
              <span style="float:right;color:#ccc">{{ item.deptno }}</span>
            </ui-option>
          </ui-select>
        </ui-form-item>
      </ui-col>
    </ui-row>
    <ui-row>
      <ui-col span="12">
        <ui-form-item prop="data_state" label="数据状态">
          <ui-input v-model="orgMsgForm.data_state"
                    placeholder="数据状态"
                    class="labelClass"
                    clearable filterable></ui-input>
        </ui-form-item>
      </ui-col>
      <ui-col span="12">
        <ui-form-item prop="arrangement" label="层级">
          <ui-input v-model="orgMsgForm.arrangement"
                    placeholder="层级"
                    class="labelClass"
                    clearable filterable></ui-input>
        </ui-form-item>
      </ui-col>
    </ui-row>
    <ui-row>
      <ui-col span="12">
        <ui-form-item prop="enabled_flag" label="有效标识">
          <ui-input v-model="orgMsgForm.enabled_flag"
                    type="text"
                    clearable
                    placeholder="请输入有效标识"
                    class="labelClass"></ui-input>
        </ui-form-item>
      </ui-col>
      <ui-col span="12">
        <ui-form-item prop="top_deptno" label="专业公司代码">
          <ui-input v-model="orgMsgForm.top_deptno"
                    type="text"
                    clearable
                    placeholder="请输入专业公司代码"
                    class="labelClass"></ui-input>
        </ui-form-item>
      </ui-col>
    </ui-row>
    <ui-row>
      <ui-col span="12">
        <ui-form-item prop="ps_uni_deptid" label="PS系统唯一键">
          <ui-input v-model="orgMsgForm.ps_uni_deptid"
                    type="text"
                    clearable
                    placeholder="请输入PS系统唯一键"
                    class="labelClass"></ui-input>
        </ui-form-item>
      </ui-col>
      <ui-col span="12">
        <ui-form-item prop="ps_top_deptid" label="PS专业公司代码">
          <ui-input v-model="orgMsgForm.ps_top_deptid"
                    type="text"
                    clearable
                    placeholder="请输入PS专业公司代码"
                    class="labelClass"></ui-input>
        </ui-form-item>
      </ui-col>
    </ui-row>
  </ui-form>
</template>

<script type="text/ecmascript-6">
  export default{
    name: 'orgForm',
    props: {
      cardHeight: Number,
      action: String,
      orgMsgForm: Object, // 表单对象
      deptData: Array,
      disabled: Boolean
    },
    data () {
      return {
        validate: { // 表单验证规则
          deptno: [{required: true, message: '请输入部门代码', trigger: 'blur'}],
          dept_name: [{required: true, message: '请输入部门名称', trigger: 'blur'}],
          dept_abbr_name: [{required: true, message: '请输入部门简称', trigger: 'blur'}],
          up_deptno: [{required: true, message: '请选择上级部门', trigger: 'blur'}],
          top_deptno: [{required: true, message: '请输入专业公司代码', trigger: 'blur'}]
        }
      };
    },
    computed: {
      /**
       * 计算form整体高度
       * 防止内容溢出
       * */
      cardBodyStyle () {
        return {
          width: '100%',
          height: `${this.cardHeight - 195}px`,
          overflow: 'auto'
        };
      }
    },
    methods: {
      /**
       * 新增修改部门
       * @param flag 标识新增、修改
       * */
      orgQuery (flag) {
        this.$emit('on-orgQuery', flag);
      },

      /**
       * 新增修改取消事件
       * */
      queryCancel () {
        this.$refs['orgMsgForm'].resetFields();
        this.$emit('on-queryCancel');
      },

      /**
       * 新增、修改确定事件
       * */
      queryEvent () {
        this.$refs['orgMsgForm'].validate((valid) => {
          if (valid) {
            this.$emit('on-queryEvent');
            this.queryCancel();
          }
        });
      }
    }
  };
</script>

<style lang="less" scoped>
  /deep/ .ivu-card-head {
    border-bottom: 1px solid #e6e6e6;
    padding: 8px;
    line-height: 1;
  }
</style>



