<template>
  <ui-card class="roleManagement-table">
    <div slot="title">
      <ui-button type="info" size="small" icon="plus" @click="handleSetting('A')">新增</ui-button>
      <ui-button type="info" size="small" icon="edit" :disabled="disabled" @click="handleSetting('U')">修改</ui-button>
      <ui-button type="error" size="small" icon="trash-a" :disabled="disabled" @click="handleSetting('D')">删除</ui-button>
      <span class="card-title-text">部门列表</span>
      <ui-icon type="document-text" class="card-title-icon"></ui-icon>
    </div>
    <div class="cardBody" :style="cardBody">
      <ui-table
          highlight-row
          :columns="columns"
          :data="orgList"
          :height="tableHeight"
          @on-row-click="handleRowClick">
      </ui-table>
    </div>
  </ui-card>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'orgTable',
    props: {
      disabled: Boolean,
      columns: Array,
      orgList: Array,
      height: Number
    },
    computed: {
      cardBody () {
        return {
          width: '100%',
          height: `${this.height - 196}px`
        };
      },
      tableHeight () {
        return this.height - 196;
      }
    },
    methods: {
      handleSetting (flag) {
        this.$emit('on-setting', flag);
      },
      handleRowClick (data, index) {
        this.$emit('on-row-click', data, index);
      }
    }
  };
</script>

<style lang="less" scoped>

</style>



