<template>
  <ui-card class="roleManagement-table">
    <div slot="title">
      <ui-input class="quick-query" v-model="searchKey" type="text" placeholder="输入角色关键字" size="small"
                icon="search" @on-change="inputChange"></ui-input>
      <ui-button type="info" size="small" icon="plus" title="新增角色"
                 @click="queryRole('add')" :disabled="btnDisable">新增
      </ui-button>
      <span class="card-title-text">角色列表</span>
      <ui-icon type="document-text" class="card-title-icon"></ui-icon>
    </div>
    <div class="cardBody" :style="cardBodyStyle">
      <ui-table :columns="roleTableColumns" :data="roleTableData" :height=tableHeight
                @on-row-click="tableRowClick" highlight-row></ui-table>
      <ui-page class="pageClass" :total="total" :page-size="pageSize" show-total
               show-sizer placement="top" size="small" :page-size-opts=[10,30,50,100]
               @on-change="pageChange" @on-page-size-change="pageSizeChange"></ui-page>
    </div>
  </ui-card>
</template>
<script type="text/ecmascript-6">
  import {copyObject} from '../../../assets/js/utils.js';

  export default{
    name: 'roleTale_page',
    props: {
      total: Number,
      pageSize: Number,
      cardHeight: Number,
      tableItemIndex: Number,
      roleTableData: Array
    },
    data () {
      return {
        searchKey: '',
        btnDisable: false,
        roleTableColumns: [
          {
            title: '角色编码',
            key: 'menuRoleId',
            align: 'center',
            render: (h, params) => {
              if (params.row.addFlag) {
                return h('uiTooltip',
                  {
                    ref: 'tooltip',
                    props: {
                      content: this.validateRole.roleid[0].message,
                      placement: 'bottom-end',
                      transfer: true,
                      always: true,
                      disabled: this.validateRole.roleid[0].flag !== 'false'
                    },
                    style: {
                      fontSize: '10px'
                    }
                  },
                  [
                    h('uiInput',
                      {
                        props: {
                          value: params.row.menuRoleId,
                          type: 'text',
                          size: 'small',
                          placeholder: '请输入角色编码'
                        },
                        nativeOn: {
                          click: () => {
                            event.stopPropagation(); // 阻止向上冒泡
                          }
                        },
                        on: {
                          'on-blur': (event) => {
                            let reg = this.validateRole.roleid[0].pattern;
                            if (event.target.value !== '') {
                              if (reg.test(event.target.value)) {
                                this.validateRole.roleid[0].flag = 'true';
                                this.roleQueryForm.menuRoleId = event.target.value;
                              } else {
                                this.validateRole.roleid[0].flag = 'false';
                                this.validateRole.roleid[0].message = '角色代码只能包含数字和字母';
                              }
                            } else {
                              this.validateRole.roleid[0].flag = 'false';
                              this.validateRole.roleid[0].message = '请输入角色编码';
                            }
                          }
                        }
                      }
                    )
                  ]
                );
              } else {
                return h('span', params.row.menuRoleId);
              }
            }
          },
          {
            title: '角色名称',
            key: 'description',
            align: 'center',
            render: (h, params) => {
              if (params.row.editFlag) {
                return h('uiTooltip',
                  {
                    props: {
                      content: '请输入角色名称',
                      placement: 'bottom-end',
                      transfer: true,
                      always: true,
                      disabled: this.validateRole.rolename[0].flag !== 'false'
                    },
                    style: {
                      fontSize: '10px'
                    }
                  },
                  [
                    h('uiInput',
                      {
                        props: {
                          value: params.row.description,
                          type: 'text',
                          size: 'small',
                          placeholder: '请输入角色名称'
                        },
                        nativeOn: {
                          click: () => {
                            event.stopPropagation(); // 阻止向上冒泡
                          }
                        },
                        on: {
                          'on-blur': (event) => {
                            if (event.target.value !== '') {
                              this.validateRole.rolename[0].flag = 'true';
                              this.roleQueryForm.description = event.target.value;
                            } else {
                              this.validateRole.rolename[0].flag = 'false';
                            }
                          }
                        }
                      }, [params.row.rolename]
                    )
                  ]
                );
              } else {
                return h('span', params.row.description);
              }
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            width: 200,
            render: (h, params) => {
              const handleBtn = (vm, h, params) => {
                return h('uiButtonGroup',
                  {
                    props: {
                      size: 'small'
                    }
                  }, [
                    h('uiButton',
                      {
                        props: {
                          type: 'info',
                          icon: 'edit',
                          disabled: this.tableItemIndex !== params.index
                        },
                        style: {
                          marginRight: '5px'
                        },
                        on: {
                          click: () => {
                            if (!this.roleQueryForm.addFlag) {
                              this.queryRole('edit');
                            }
                            event.stopPropagation(); // 阻止向上冒泡
                          }
                        }
                      }, '修改'
                    ),
                    h('uiButton',
                      {
                        props: {
                          type: 'error',
                          icon: 'trash-a',
                          disabled: this.tableItemIndex !== params.index
                        },
                        on: {
                          click: () => {
                            this.$emit('on-delRole');
                            event.stopPropagation(); // 阻止向上冒泡
                          }
                        }
                      }, '删除'
                    )
                  ]);
              };
              const queryBtn = (vm, h, params) => {
                return h('uiButtonGroup',
                  {
                    props: {
                      size: 'small'
                    }
                  }, [
                    h('uiButton',
                      {
                        props: {
                          type: 'text'
                        },
                        style: {
                          marginRight: '5px'
                        },
                        on: {
                          click: () => {
                            vm.btnDisable = false;
                            vm.validateRole.roleid[0].flag = 'init';
                            vm.validateRole.rolename[0].flag = 'init';
                            vm.queryCancel();
                            event.stopPropagation(); // 阻止向上冒泡
                          }
                        }
                      }, '取消'
                    ),
                    h('uiButton',
                      {
                        props: {
                          type: 'primary',
                          icon: 'checkmark-round'
                        },
                        on: {
                          click: () => {
                            if (vm.validateRole.roleid[0].flag === 'true' &&
                              vm.validateRole.rolename[0].flag === 'true' &&
                              vm.validateRole.roleid[0].flag === 'true') {
                              vm.$emit('on-queryEvent', this.roleQueryForm);
                              vm.btnDisable = false;
                              this.roleQueryForm = {
                                menuRoleId: '',
                                description: '',
                                arrangement: '',
                                dataState: '',
                                subsystemId: ''
                              };
                            } else {
                              for (let prop in vm.validateRole) {
                                if (vm.validateRole[prop][0].flag === 'init') {
                                  vm.validateRole[prop][0].flag = 'false';
                                }
                              }
                            }
                            event.stopPropagation(); // 阻止向上冒泡
                          }
                        }
                      }, '确认'
                    )
                  ]
                );
              };
              return h('div', {}, [params.row.addFlag || params.row.editFlag
                ? queryBtn(this, h, params) : handleBtn(this, h, params)]);
            }
          }
        ],
        roleQueryForm: {
          menuRoleId: '',
          description: '',
          arrangement: '',
          dataState: '',
          subsystemId: ''
        },
        validateRole: {
          roleid: [
            {flag: 'init', pattern: /^[A-Za-z0-9]+$/, message: '请输入角色编码'}
          ],
          rolename: [
            {flag: 'init', message: '请输入角色名称'}
          ],
          orgid: [
            {flag: 'init', message: '请选择所属部门'}
          ]
        }
      };
    },
    computed: {
      cardBodyStyle () {
        return {
          width: '100%',
          height: `${this.cardHeight - 196}px`
        };
      },
      tableHeight: function () {
        return this.cardHeight - 196 - 40.8;
      }
    },
    methods: {
      /**
       * 快速搜索角色
       * @param event
       * */
      inputChange () {
        this.$emit('on-inputChange', this.searchKey);
      },

      /**
       * 表格数据点击
       * */
      tableRowClick (data, index) {
        if (this.roleQueryForm.action === 'U' || this.roleQueryForm.action === 'A') {
          return false;
        }
        if (data.editFlag || data.addFlag) {
          return false;
        }
        this.roleQueryForm = copyObject(data);
        this.btnDisable = false;
        this.$emit('on-tableRowClick', data, index);
      },

      /**
       * 新增、修改角色
       * */
      queryRole (flag) {
        if (flag === 'add') {
          this.roleQueryForm = {};
          this.roleQueryForm.action = 'A';
          this.roleQueryForm.parentroleid = '';
          this.roleQueryForm.editFlag = true;
          this.roleQueryForm.addFlag = true;
          this.validateRole.roleid[0].flag = 'init';
          this.validateRole.rolename[0].flag = 'init';
        } else if (flag === 'edit') {
          this.roleQueryForm.action = 'U';
          this.validateRole.roleid[0].flag = 'true';
          this.validateRole.rolename[0].flag = 'true';
        }
        this.$emit('on-queryRole', flag, this.roleQueryForm);
        this.btnDisable = true;
      },

      /**
       * 新增、修改角色取消
       * */
      queryCancel () {
        let flag = this.roleQueryForm.action;
        this.$emit('on-queryCancel', flag);
        this.roleQueryForm = {
          menuRoleId: '',
          description: '',
          arrangement: '',
          dataState: '',
          subsystemId: ''
        };
      },

      /**
       * 页码改变
       * */
      pageChange (page) {
        this.$emit('on-pageChange', page);
      },

      pageSizeChange (pageSize) {
        this.$emit('on-pageSizeChange', pageSize);
      }
    }
  };
</script>
<style lang="less" scoped>
  .roleManagement-table {
    /deep/ .ivu-card-head {
      border-bottom: 1px solid #e6e6e6;
      padding: 8px;
      line-height: 1;
    }
  }
</style>



