<template>
  <div class="seniorSearch">
    <transition :name="transitionNames[0]">
      <div class="ivu-seniorSearch-mask" v-show="visible"></div>
    </transition>
    <transition :name="transitionNames[1]">
      <div :style="seniorSearchStyle" class="ivu-seniorSearch-wrap" v-show="visible" @click="handWrap">
        <div class="ivu-seniorSearch" :style="styles">
          <div class="ivu-seniorSearch-content">
            <div class="ivu-seniorSearch-header" v-if="showHead">
              <slot name="header">
                <div class="ivu-seniorSearch-header-inner">
                  <ui-icon class="cardTitleIcon" type="search-plus"></ui-icon>
                  <span class="cardTitleClass">{{ title }}</span>
                </div>
              </slot>
            </div>
            <div class="ivu-seniorSearch-body">
              <slot></slot>
            </div>
            <div class="ivu-seniorSearch-footer">
              <slot name="footer">
                <ui-button type="primary" icon="checkmark" @click="ok">确定</ui-button>
                <div class="split-style"></div>
                <ui-button type="info"  icon="ios-loop-strong" @click="reset">重置</ui-button>
                <div class="split-style"></div>
                <ui-button type="text" @click="cancel" style="padding: 6px 0">取消</ui-button>
              </slot>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <ui-button type="info" :size="btnSize" icon="search-plus" @click="btnClick" v-if="hasButton">{{ btnText }}</ui-button>
  </div>
</template>
<script type="text/ecmascript-6">
  export default{
    name: 'seniorSearch-page',
    props: {
      visible: {
        type: Boolean, // 是否显示表单
        default: false
      },
      width: {
        type: Number // 表单宽度
      },
      height: {
        type: Number // 表单高度
      },
      formStyle: {
        type: Object // 表单样式
      },
      btnText: {
        type: String, // 按钮显示文字
        default: '高级搜索'
      },
      btnSize: {
        type: String, // 按钮大小
        default: 'small'
      },
      showHead: {
        type: Boolean, // 是否显示头部
        default: true
      },
      title: {
        type: String, // 头部标题
        default: '高级搜索'
      },
      transitionNames: {
        type: Array, // 搜索框显示、隐藏的动画效果
        default () {
          return ['fade', 'wrap'];
        }
      },
      hasButton: {
        type: Boolean, // 是否需要按钮
        default: true
      }
    },
    methods: {
      /**
       * 点击搜索框事件
       * */
      btnClick () {
        this.$emit('on-btnClick');
      },

      /**
       * 点击遮罩层
       * @param event
       */
      handWrap (event) {
        const className = event.target.getAttribute('class');
        if (className && className.indexOf('ivu-seniorSearch-wrap') > -1) {
          this.cancel();
        }
      },

      /**
       * 重置表单
       * */
      reset () {
        this.$emit('on-reset');
      },

      /**
       * 取消
       * */
      cancel () {
        this.$emit('on-cancel');
      },

      /**
       * 确定
       * */
      ok () {
        this.$emit('on-ok');
      }
    },
    computed: {
      styles () {
        return {
          height: `${this.height}px`,
          overflow: 'auto'
        };
      },
      seniorSearchStyle () {
        let style = {
          width: `${this.width}px`
        };
        Object.assign(style, this.formStyle);
        return style;
      }
    }
  };
</script>
<style lang="less" scoped>
  .seniorSearch {
    display: inline-block;
    .ivu-seniorSearch {
      &-mask {
        position: fixed;
        top: 110px;
        bottom: 0;
        left: 10px;
        right: 10px;
        background-color: rgba(233, 243, 248, 0.4);
        height: 100%;
        z-index: 1000;
      }
      &-wrap {
        position: fixed;
        overflow: auto;
        top: 110px;
        right: 10px;
        bottom: 0;
        left: 10px;
        z-index: 1000;
        -webkit-overflow-scrolling: touch;
        outline: 0;
      }
      &-content {
        position: relative;
        background-color: #fff;
        border: 0;
        border-radius: 6px;
        background-clip: padding-box;
      }
      &-close {
        position: absolute;
        right: 16px;
        top: 8px;
        overflow: hidden;
      }
      &-header {
        border-bottom: 1px solid #e9eaec;
        padding: 8px;
        line-height: 1;
        &-inner {
          display: inline-block;
          width: 100%;
          height: 24px;
          line-height: 20px;
          font-size: 14px;
          color: #1c2438;
          font-weight: 700;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
      &-body {
        padding: 16px;
        font-size: 12px;
        line-height: 1.5;
      }
      &-footer {
        border-top: 1px solid #e9eaec;
        padding: 12px 18px;
        text-align: center;
      }
    }
  }

  .btnClass {
    &:hover {
      background: aliceblue;
    }
  }
  .cardTitleClass{
    margin-right: 8px;
    margin-top: 6px;
  }
  .cardTitleIcon{
    margin-top: 9px;
    margin-right: 2px;
    margin-left: 2px;
    font-size: 15px;
  }
  .wrap-enter {
    transform: translateY(-110px);
    opacity: 0;
  }

  .wrap-enter-active, .wrap-leave {
    transition: all 0.7s ease;
  }

  .wrap-leave-active {
    transition: all 0.7s ease;
    transform: translateY(-100px);
    opacity: 0;
  }
</style>



