<template>
  <ui-tabs type="card" v-model="tabsName" :animated="true" @on-click="tabsClick">
    <ui-button slot="extra" type="text" icon="close-circled" title="关闭" @click="closeRight"
               v-if="!btnShow&&closeBtnShow"></ui-button>
    <ui-tab-pane v-for="tab in tabsObj" :key="tabsObj.name" :label=tab.label :name=tab.name :disabled="btnDisable"
                 v-if="tab.isShow">
      <div class="tabsBody" :style="tabsBodyStyle">
        <div class="tabBtn" style="padding: 10px;width: 100%;">
          <ui-button type="primary" @click="editRoleRight" :disabled="btnDisable" size="small" shape="circle"
                     v-show="!btnShow" style="float: right">修改权限
          </ui-button>
          <ui-button type="primary" title="" @click="editRightQuery" size="small" shape="circle"
                     v-show="btnShow" style="float: right;">确定
          </ui-button>
          <ui-button type="error" title="" @click="editRightCancel" size="small" shape="circle"
                     v-show="btnShow" style="float: right">取消
          </ui-button>
        </div>
        <div class="tabTree" :style="tabTreeStyle">
          <ui-tree :data="rightTreeList" show-checkbox :ref=tab.name :render="treeListRender"></ui-tree>
        </div>
      </div>
    </ui-tab-pane>
    <ui-tab-pane label="复制权限" name="copyRight" :disabled="btnDisable" v-if="this.closeBtn">
      <div class="tabsBody" :style="tabsBodyStyle">
        <ui-form :label-width="100" :model="copyRightForm" ref="copyRightForm"
                 style="margin-top: 30px;" :rules="validateForm">
          <ui-row>
            <ui-col>
              <ui-form-item prop="copyrightflag" label="权限类型">
                <ui-checkbox :indeterminate="indeterminate" :value="checkAll"
                             @click.prevent.native="handleCheckAll">全选
                </ui-checkbox>
                <ui-checkboxGroup v-model="copyRightForm.copyrightflag"
                                  @on-change="checkAllGroupChange">
                  <ui-checkbox :label="item.subitem " v-for="item in rightTypeList" :key="rightTypeList.subitem">
                    <span>{{item.subitemname}}</span>
                  </ui-checkbox>
                </ui-checkboxGroup>
              </ui-form-item>
            </ui-col>
          </ui-row>
          <ui-row>
            <ui-col span="10">
              <ui-form-item>
                <ui-button @click="closeRight">取消</ui-button>
              </ui-form-item>
            </ui-col>
            <ui-col span="10">
              <ui-form-item>
                <ui-button type="primary" @click="copyRightQuery">确定</ui-button>
              </ui-form-item>
            </ui-col>
          </ui-row>
        </ui-form>
      </div>
    </ui-tab-pane>
  </ui-tabs>
</template>
<script type="text/ecmascript-6">
  import {copyObject} from '@/assets/js/utils.js';

  export default{
    props: {
      height: Number,
      btnDisable: Boolean,
      closeBtn: Boolean,
      rightTreeData: Array
    },
    data () {
      return {
        tabsName: '',
        btnShow: false,
        closeBtnShow: false,
        checkAll: true,
        indeterminate: true,
        userList: [],
        rightTreeList: [],
        rightTreeList1: [],
        tabsObj: [
          {label: '关联用户', name: 'linkUser', isShow: !this.closeBtn},
          {label: '执行权限', name: 'executeRight', isShow: true},
          {label: '授权权限', name: 'authorizeRight', isShow: this.closeBtn}
        ],
        rightTypeList: [],
        copyRightForm: {
          userid: '',
          copyrightflag: []
        },
        validateForm: {
          userid: [{required: true, message: '请选择用户', trigger: 'blur'}],
          copyrightflag: [{required: true, type: 'array', message: '请选择权限类型', trigger: 'blur'}]
        }
      };
    },
    computed: {
      tabsBodyStyle () {
        return {
          width: '100%',
          height: this.height,
          marginTop: '-16px',
          border: 'solid 1px #e6e6e6'
        };
      },
      tabTreeStyle () {
        return {
          width: '100%',
          height: `${this.height - 50}px`,
          padding: '16px',
          overflow: 'auto'
        };
      }
    },
    methods: {
      treeListRender (h, {root, node, data}) {
        return h('span', {
          style: {
            display: 'inline-block'
          }
        }, [
          h('span', [
            h('Icon', {
              props: {
                type: 'person',
                color: data.color
              },
              style: {
                marginRight: '5px',
                width: '8px'
              }
            }),
            h('span', data.title)
          ])
        ]);
      },
      tabsClick (name) {
        this.btnShow = false;
        if (this.closeBtn) {
          this.$refs['copyRightForm'].resetFields();
        }
        this.$emit('on-tabsClick', name);
      },
      editRoleRight () {
        this.btnShow = true;
        for (let i = 0; i < this.rightTreeList.length; i++) {
          this.$set(this.rightTreeList[i], 'disabled', false);
        }
      },
      closeRight () {
        this.$refs['copyRightForm'].resetFields();
        if (this.closeBtnShow) {
          this.$emit('on-closeRightTabs');
        } else {
          return false;
        }
      },
      editRightCancel () {
        this.btnShow = false;
        this.rightTreeList = copyObject(this.rightTreeList1);
      },
      editRightQuery () {
        let data = this.$refs[this.tabsName][0].getCheckedNodes();
        this.$emit('on-editRightQuery', data);
        this.btnShow = false;

      },
      handleCheckAll () {
        if (this.indeterminate) {
          this.checkAll = false;
        } else {
          this.checkAll = !this.checkAll;
        }
        this.indeterminate = false;
        if (this.checkAll) {
          for (let i = 0; i < this.rightTypeList.length; i++) {
            this.copyRightForm.copyrightflag.push(this.rightTypeList[i].subitem);
          }
        } else {
          this.copyRightForm.copyrightflag = [];
        }
      },
      checkAllGroupChange (data) {
        if (data.length === this.rightTypeList.length) {
          this.indeterminate = false;
          this.checkAll = true;
        } else if (data.length > 0) {
          this.indeterminate = true;
          this.checkAll = false;
        } else {
          this.indeterminate = false;
          this.checkAll = false;
        }
      },
      copyRightQuery () {
        let data = {};
        this.$refs['copyRightForm'].validate((valid) => {
          if (valid) {
            data.sourceuserid = this.copyRightForm.userid;
            data.copyrightflag = this.copyRightForm.copyrightflag.join(',');
            this.$emit('on-editRightQuery', data);
            this.closeRight();
          }
        });
      }
    },
    watch: {
      rightTreeData (val) {
        this.rightTreeList = copyObject(val);
        this.rightTreeList1 = copyObject(val);
      }
    },
    created () {
      this.tabsName = this.closeBtn ? 'executeRight' : 'linkUser';
      this.closeBtnShow = this.closeBtn;
    }
  };
</script>
<style lang="less" scoped>
  /deep/ .ivu-checkbox-wrapper {
    margin-right: 3px;
  }
</style>



