<template>
  <div class="page">
    <div class="page-panel" :style="styles">
      <div class="roleManagement">
        <ui-row>
          <ui-col span="16" class="colPadding">
            <role-table
                :total="total"
                :pageSize="pageSize"
                :cardHeight="windowsSize"
                :roleTableData="roleTableData"
                :tableItemIndex="tableItemIndex"
                @on-inputChange="inputChange"
                @on-queryRole="queryRole"
                @on-queryCancel="queryCancel"
                @on-queryEvent="queryEvent"
                @on-delRole="delRole"
                @on-tableRowClick="roleTableRowClick"
                @on-pageChange="pageChange"
                @on-pageSizeChange="pageSizeChange">
            </role-table>
          </ui-col>
          <ui-col span="8" class="colPadding">
            <right-tabs
                ref="rightTabs"
                :height="rightTreeHeight"
                :btnDisable="btnDisable"
                :closeBtn=false
                :rightTreeData="roleRightList"
                @on-tabsClick="tabsClick"
                @on-editRightQuery="editRightQuery"
                style="background-color: #fff">
            </right-tabs>
          </ui-col>
        </ui-row>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import orgList from '../orgList.vue';
  import rightTabs from '../rightTabs.vue';
  import roleTable from './roleTable.vue';
  import {dealRightData, getParam} from '../pageCommon.js';
  import {copyObject, treeDataStandard} from '@/assets/js/utils';

  export default{
    name: 'RoleManagement-page',
    components: {
      orgList,
      rightTabs,
      roleTable
    },
    data () {
      return {
        tableItemIndex: -1,
        total: 0,
        pageSize: 30,
        searchTableKey: '',
        tabname: 'linkUser',
        windowsSize: document.body.clientWidth,
        roleQueryModal: false,
        btnDisable: true,
        orgList: [],
        roleValue: [],
        orgTreeData: [],
        treeSelectData: {},
        roleTableData: [],
        tableData: [],
        roleRightList: [],
        haveRightData: [],
        roleRowData: {},
        roleRightList1: {}
      };
    },
    methods: {
      /**
       * 机构树选中事件
       * @param  data  选中条目的数据
       * */
      treeSelected () {
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101105',
            param: {}
          },
          success: () => {
            let responseData = this.$store.getters.jsonData;
            if (responseData) {
              if (responseData.flag === '0') {
                let data = [];
                let record = responseData.results[0].record;
                for (let i = 0; i < record.length; i++) {
                  if (record[i].deptno === this.treeSelectData.deptno ||
                    this.treeSelectData.deptno === undefined) {
                    data.push(record[i]);
                  }
                }
                this.tableData = data;
                this.total = this.tableData.length;
                this.roleTableData = this.tableData.slice(0, this.pageSize);
              } else {
                this.$uiNotice.error({
                  title: '查询机构下角色失败',
                  desc: responseData.result
                });
              }
            }
            this.inputChange(this.searchTableKey);
          }
        });
      },

      /**
       * 遍历树节点
       * @param key：输入框的值
       * @param data：遍历的数据
       * */
      ergodicOrgList (key, data) {
        for (let i = 0; i < data.length; i++) {
          if (data[i].orgname.indexOf(key) > -1) {
            this.orgTreeData.push(data[i]);
          } else if (data[i].orgid.indexOf(key) > -1) {
            this.orgTreeData.push(data[i]);
          } else if (data[i]['children']) {
            this.ergodicOrgList(key, data[i]['children']);
          }
        }
      },

      /**
       * 角色增加、修改事件
       * */
      queryRole (flag, formObj) {
        if (flag === 'add') {
          this.roleRightList = []; // 清空权限数据
          this.tableItemIndex = 0;
          this.roleTableData.unshift(copyObject(formObj));
        } else if (flag === 'edit') {
          this.$set(this.roleTableData[this.tableItemIndex], 'editFlag', true);
        }
      },

      /**
       *新增修改取消事件
       **/
      queryCancel (flag) {
        if (flag === 'A') {
          this.roleTableData.shift();
        } else if (flag === 'U') {
          this.$set(this.roleTableData[this.tableItemIndex], 'editFlag', false);
        }
        this.tableItemIndex = -1;
        this.roleRowData = {};
      },

      /**
       * 表格数据选中事件
       * @param data  表格选中数据
       * */
      roleTableRowClick (data, index) {
        if (this.roleRowData.menuRoleId !== data.menuRoleId) {
          if (this.$refs['rightTabs']) {
            this.$refs['rightTabs'].btnShow = false;
          }
          this.btnDisable = this.roleRightList === [] ? true : false;
          this.roleRowData = data;
          this.tableItemIndex = index;
          this.roleRightList1 = {};
          this.tabsClick(this.tabname);
        }
      },

      /**
       * 页码改变
       * @param page 页码
       * */
      pageChange (page) {
        this.roleTableData = this.tableData.slice((page - 1) * this.pageSize, page * this.pageSize);
      },

      /**
       * 页数改变事件
       * @param pageSize 显示页数
       * */
      pageSizeChange (pageSize) {
        this.pageSize = pageSize;
        this.roleTableData = this.tableData.slice(0, pageSize);
      },

      /**
       * 角色表格搜索
       * @param key 输入框的值
       * */
      inputChange (key) {
        this.searchTableKey = key;
        let data = [];
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].description.indexOf(key) > -1) {
            data.push(this.tableData[i]);
          } else if (this.tableData[i].menuRoleId.indexOf(key) > -1) {
            data.push(this.tableData[i]);
          }
        }
        this.roleTableData = data.slice(0, this.pageSize);
      },

      /**
       * 页签点击事件
       * @param name 选中的页签的名称
       * */
      tabsClick (name) {
        this.tabname = name;
        if (!this.roleRightList1[this.tabname]) {
          switch (name) {
            case 'linkUser':
              this.getRoleUser();
              break;
            case 'executeRight':
              this.getExecuteRight();
              break;
            case 'orgRight':
              this.getOrgRightData();
              break;
            case 'msgRight':
              this.getMsgRightData();
              break;
          }
        } else {
          this.roleRightList = this.roleRightList1[name];
        }
      },

      /**
       * 获取用户，有角色的默认选中
       * */
      getRoleUser () {
        let that = this;
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101106',
            param: {menuRoleId: this.roleRowData.menuRoleId}
          },
          success: function () {
            let responseData = that.$store.getters.jsonData;
            if (responseData) {
              if (responseData.flag === '0') {
                let record = responseData.results[0].record;
                let userList = copyObject(record);
                record.forEach((param, index) => {
                  if (param.ischoose === '1') {
                    that.$set(userList[index], 'checked', true);
                  }
                });
                that.roleRightList = userList;
                for (let p = 0; p < that.roleRightList.length; p++) {
                  that.$set(that.roleRightList[p], 'disabled', true);
                  that.$set(that.roleRightList[p], 'title',
                    `${that.roleRightList[p].username}-${that.roleRightList[p].userid}`);
                }
                that.roleRightList1[that.tabname] = copyObject(that.roleRightList);
              } else {
                that.$uiNotice.error({
                  title: '查询角色关联用户失败',
                  desc: responseData.prompt
                });
              }
            }
          }
        });
      },

      /**
       * 获取执行权限 角色菜单
       * */
      getExecuteRight () {
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101404',
            param: {menuRoleId: this.roleRowData.menuRoleId}
          },
          success: () => {
            let responseData = this.$store.getters.jsonData;
            if (responseData) {
              if (responseData.flag === '0') {
                let record = responseData.results[0].record;
                this.roleRightList = dealRightData(this, 'executeRight', record).list;
                this.haveRightData = dealRightData(this, 'executeRight', record).data;
                for (let p = 0; p < this.roleRightList.length; p++) {
                  this.$set(this.roleRightList[p], 'disabled', true);
                }
                this.roleRightList1[this.tabname] = copyObject(this.roleRightList);
              } else {
                this.$uiNotice.error({
                  title: '查询角色执行权限失败',
                  desc: responseData.prompt
                });
              }
            }
          }
        });
      },


      /**
       * 修改权限确认按钮
       * @param name 当前页签的名称
       * */
      editRightQuery (checkData) {
        let objCode = '';
        let param = {};
        let data = checkData; // 获取被勾选的节点
        switch (this.tabname) {
          case 'linkUser':
            objCode = '101107';
            let useridArray = [];
            for (let i = 0; i < data.length; i++) { // 遍历选中的数据
              useridArray.push(data[i].userid); // 将数据放入到orgidManyy中
            }
            param = {
              'menuRoleId': this.roleRowData.menuRoleId,
              'userId': useridArray.join(',')
            };
            this.requestRightBex(objCode, param, this.tabname);
            break;
          case 'executeRight':
            objCode = '101303';
            param = getParam(this, this.haveRightData, data);
            if (param === '') {
              this.tabsClick(this.tabname);
              return false;
            }
            this.requestRightBex(objCode, param, this.tabname);
            break;
        }
      },

      /**
       * 请求权限设置接口
       * */
      requestRightBex (objCode, param, name) {
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: objCode,
            param: param,
            async: true
          },
          success: () => {
            let responseData = this.$store.getters.jsonData;
            if (responseData) {
              if (responseData.flag === '0') {
                this.$uiNotice.success({
                  title: '设置权限成功'
                });
              } else {
                this.$uiNotice.error({
                  title: '设置权限失败',
                  desc: responseData.prompt
                });
              }
              this.roleRightList1[this.tabname] = undefined;
              this.tabsClick(name);
            }
          }
        });
      },

      /**
       * 新增、修改表单数据确认事件
       * @param param 表单数据
       * */
      queryEvent (param) {
        let objCode = param.action === 'U' ? '101103' : '101102';
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: objCode,
            param: param
          },
          success: () => {
            let responseData = this.$store.getters.jsonData;
            if (responseData) {
              if (responseData.flag === '0') {
                this.$uiNotice.success({
                  title: '操作成功'
                });
                this.roleRowData = {};
                this.tableItemIndex = -1;
                this.roleRightList = [];
                this.btnDisable = true;
              } else {
                this.$uiNotice.error({
                  title: '操作失败',
                  desc: responseData.prompt
                });
              }
              this.treeSelected([this.treeSelectData]);
            }
          }
        });
      },

      /**
       * 删除角色
       * @param param 删除的行数据
       * */
      delRole () {
        this.$uiModal.confirm({
          title: '删除',
          content: '确定删除该角色？',
          onOk: () => {
            let that = this;
            this.roleRowData.action = 'D'; // 删除角色
            this.roleRowData.sysid = '1'; // 系统代码
            this.roleRowData.rolestatus = '';
            this.$store.dispatch('ajaxCallLbm', {
              data: {
                obj_code: '101104',
                param: this.roleRowData
              },
              success: function () {
                let response = that.$store.getters.jsonData;
                if (response) {
                  if (response.flag === '0') {
                    that.$uiNotice.success({
                      title: '删除角色成功'
                    });
                  } else {
                    that.$uiNotice.error({
                      title: '删除角色失败',
                      desc: response.prompt
                    });
                  }
                  that.roleRowData = {};
                  that.tableItemIndex = -1;
                  that.treeSelected([that.treeSelectData]);
                }
              }
            });
          }
        });
      },
      /**
       * 查询机构信息
       */
      getOrgData () {
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101205',
            param: {deptno: '001'}
          },
          success: () => {
            // TODO 需要处理数据，存到data变量中
            let responseData = this.$store.getters.jsonData;
            if (responseData.flag === '0') {
              let record = responseData.results[0].record;
              this.orgTreeData = [{
                title: '所有部门',
                children: treeDataStandard(record, '', 'up_deptno', 'deptno')
              }];
              this.$set(this.orgTreeData[0], 'expand', true);
            } else {
              this.$uiNotice.error({
                title: '查询部门信息失败',
                desc: responseData.prompt
              });
            }
          }
        });
      }
    },
    computed: {
      styles () {
        return {
          height: `${this.windowsSize - 101}px`
        };
      },
      rightTreeHeight () {
        return this.windowsSize - 138;
      }
    },
    mounted () {
      this.windowsSize = Number(document.documentElement.clientHeight);
      let that = this;
      window.onresize = function temp () {
        that.windowsSize = Number(document.documentElement.clientHeight);
      };
    },
    created () {
      this.treeSelected();
      // this.getOrgData();
    }
  };
</script>

<style lang="less" scoped>
  .roleManagement {
    width: 100%;
    height: 100%;
    .ivu-tree {
      width: 100%;
      /deep/ .ivu-tree-arrow {
        cursor: pointer;
        width: 12px;
        text-align: center;
        display: inline-block;
      }
    }
    /deep/ textarea.ivu-input {
      font-size: 12px;
    }
  }

  /deep/ .treeListClass {
    display: inline-block;
    width: 100%;
    &:hover {
      cursor: pointer;
    }
  }

  /deep/ .treeClickClass {
    background-color: #ebf7ff;
  }

  /deep/ .ivu-select-dropdown {
    z-index: 1000;
  }
</style>



