<template>
  <div>
    <ui-form :label-width="100" :model="userQueryForm" ref="queryForm" :rules="validateUser" v-show="isHandleForm">
      <ui-row>
        <ui-col span="12">
          <ui-form-item prop="deptno" label="部门编号">
            <ui-select v-model="userQueryForm.deptno" placeholder="请选择所属部门" :disabled="itemDisable">
              <ui-option v-for="item in deptData" :value="item.deptno" :key="deptData.deptno"
                         :label="item.dept_name">
                <span>{{item.dept_name}}</span>
                <span style=" float:right;color:#ccc">{{ item.deptno }}</span>
              </ui-option>
            </ui-select>
          </ui-form-item>
        </ui-col>
        <ui-col span="12">
          <ui-form-item prop="user_type" label="用户类型">
            <ui-input v-model="userQueryForm.user_type" placeholder="用户类型" :disabled="itemDisable"
                      :maxlength="1"></ui-input>
          </ui-form-item>
        </ui-col>
      </ui-row>
      <ui-row>
        <ui-col span="12">
          <ui-form-item prop="user_id" label="用户代码">
            <ui-input type="text" v-model="userQueryForm.user_id" placeholder="用户代码" :maxlength='16'
                      :disabled="itemDisable" autofocus></ui-input>
          </ui-form-item>
        </ui-col>
        <ui-col span="12">
          <ui-form-item prop="user_name" label="用户名称">
            <ui-input v-model="userQueryForm.user_name" placeholder="用户名称" :maxlength='30'
                      :disabled="itemDisable"></ui-input>
          </ui-form-item>
        </ui-col>
      </ui-row>
      <ui-row>
        <ui-col span="12">
          <ui-form-item prop="user_pwd" label="登录密码" v-if="!itemDisable">
            <ui-input v-model="userQueryForm.user_pwd" type="password" placeholder="密码"></ui-input>
          </ui-form-item>
        </ui-col>
        <ui-col span="12">
          <ui-form-item prop="valid_flag" label="有效标识">
            <ui-input v-model="userQueryForm.valid_flag" type="text" placeholder="有效标识"></ui-input>
          </ui-form-item>
        </ui-col>
      </ui-row>
      <ui-row>
        <ui-col span="12">
          <ui-form-item prop="mobile" label="移动电话">
            <ui-input v-model="userQueryForm.mobile" type="text" placeholder="移动电话"></ui-input>
          </ui-form-item>
        </ui-col>
        <ui-col span="12">
          <ui-form-item prop="user_position" label="用户职位">
            <ui-input v-model="userQueryForm.user_position" type="text" placeholder="用户职位"></ui-input>
          </ui-form-item>
        </ui-col>
      </ui-row>
      <div class="expandClass">
        <ui-button type="text" :icon="isOtherCondition ? 'double-arrow-up' : 'double-arrow-down'"
                   @click="expandOtherCondition" style="position: relative;background-color: white">
        </ui-button>
      </div>
      <div v-show="isOtherCondition">
        <ui-row>
          <ui-col span="12">
            <ui-form-item prop="id_code" label="证件号码">
              <ui-input v-model="userQueryForm.id_code" placeholder="证件号码"></ui-input>
            </ui-form-item>
          </ui-col>
          <ui-col span="12">
            <ui-form-item prop="paic_leave_date" label="离司日期">
              <ui-input v-model="userQueryForm.paic_leave_date" type="text" placeholder="离司日期"></ui-input>
            </ui-form-item>
          </ui-col>
        </ui-row>
        <ui-row>
          <ui-col span="12">
            <ui-form-item prop="tel" label="手机号码">
              <ui-input v-model="userQueryForm.tel" placeholder="手机号码"></ui-input>
            </ui-form-item>
          </ui-col>
          <ui-col span="12">
            <ui-form-item prop="ext" label="分机电话">
              <ui-input v-model="userQueryForm.ext" placeholder="分机电话"></ui-input>
            </ui-form-item>
          </ui-col>
        </ui-row>
        <ui-row>
          <ui-col span="12">
            <ui-form-item prop="mail" label="电子邮件">
              <ui-input v-model="userQueryForm.mail" type="text" placeholder="电子邮件"></ui-input>
            </ui-form-item>
          </ui-col>
          <ui-col span="12">
            <ui-form-item prop="fax" label="传真">
              <ui-input v-model="userQueryForm.fax" placeholder="传真"></ui-input>
            </ui-form-item>
          </ui-col>
        </ui-row>
        <ui-row>
          <ui-col>
            <ui-form-item prop="data_state" label="数据状态">
              <ui-input v-model="userQueryForm.data_state" placeholder="数据状态"></ui-input>
            </ui-form-item>
          </ui-col>
        </ui-row>
      </div>
    </ui-form>
  </div>
</template>
<script type="text/ecmascript-6">

  export default {
    name: 'userform_page',
    props: {
      itemDisable: Boolean, // 是否使字段失效
      isHandleForm: Boolean, // 是否显示表单
      userQueryForm: Object,
      deptData: Array
    },
    data () {
      return {
        isOtherCondition: false, // 控制其他信息是否展开
        validateUser: {
          user_id: [
            {required: true, message: '请输入用户代码', trigger: 'blur'}
          ],
          user_name: [
            {required: true, message: '请输入用户名称', trigger: 'blur'}
          ],
          deptno: [
            {required: true, message: '请输入部门编号', trigger: 'change'}
          ],
          user_pwd: [
            {required: true, message: '请输入密码', trigger: 'blur'}
          ],
          user_type: [
            {required: true, message: '请输入用户类型', trigger: 'blur'}
          ],
          valid_flag: [
            {required: true, message: '请输入有效标识', trigger: 'blur'}
          ]
        }
      };
    },
    methods: {
      /**
       * 新增、修改、查看表单取消
       */
      queryCancel () {
        this.$parent.isHandleForm = false;
        this.$parent.setUser = false;
      },

      /**
       * 新增、修改、查看表单确定
       */
      queryEvent () {
        this.$refs['queryForm'].validate((valid) => {
          if (valid) {
            this.$emit('on-queryModalEvent', this.userQueryForm);
            this.queryCancel();
          }
        });
      },
      expandOtherCondition () {
        this.isOtherCondition = !this.isOtherCondition;
      }
    },
    watch: {
      isHandleForm (val) {
        if (!val) {
          this.$refs['queryForm'].resetFields();
        }
      }
    }
  };
</script>

<style lang="less" scoped>
  .expandClass {
    position: relative;
    text-align: center;
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background: #eee;
      position: absolute;
      top: 15px;
      left: 0;
    }
  }

  .ivu-row {
    margin-top: 10px;
  }
</style>



