<template>
  <div class="page">
    <div class="page-panel" :style="styles">
      <div class="userManagement">
        <ui-row>
          <ui-col span="5">
            <org-list
              :height="height"
              :operFlag="false"
              :searchFlag="false"
              :data="userTreeData"
              :selData="treeSelectData"
              @on-select-change="treeSelected">
            </org-list>
          </ui-col>
          <ui-col span="19" class="colPadding">
            <user-table :windowsSize="height"
                        :total="total"
                        :pageSize="pageSize"
                        :btnDisable="btnDisable"
                        :userTableData="userTableData"
                        :isIcon="isIcon"
                        :deptData="deptData"
                        @on-tableRowClick="userTableRowClick"
                        @on-searchModalEvent="searchModalEvent"
                        @on-queryFormEvent="queryModalEvent"
                        @on-queryUser="queryUser"
                        @on-pageChange="pageChange"
                        @on-pageSizeChange="pageSizeChange">
            </user-table>
          </ui-col>
        </ui-row>
        <common-dialog :visible="userForm"
                       :width="800"
                       :height="450"
                       :title="formTitle"
                       @on-cancel="queryFormCancel"
                       @on-submit="queryModalEvent">
          <div slot="content">
            <user-form ref="setUserForm"
                       :itemDisable="itemDisable"
                       :isHandleForm="userForm"
                       :userQueryForm="userQueryForm"
                       :deptData="deptData"
                       @on-queryModalEvent="queryModalEvent">
            </user-form>
          </div>
        </common-dialog>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import orgList from '../orgList.vue';
  import rightTabs from '../rightTabs.vue';
  import userTable from './userTable.vue';
  import userForm from './userForm.vue';
  import {enc, copyObject, cnvToTree} from '@/assets/js/utils';
  import CommonDialog from '../../commons/commonDialog';

  export default{
    name: 'userManagement-page',
    components: {
      CommonDialog,
      orgList,
      rightTabs,
      userTable,
      userForm
    },
    data () {
      return {
        deptData: [],
        formTitle: '',
        total: 0,
        pageSize: 30,
        tableSearchKey: '',
        height: document.body.clientHeight,
        btnDisable: true,
        setUser: false, // 是否进行新增、修改、查看、权限操作
        itemDisable: false,
        userForm: false,
        rightTab: false,
        userTreeData: [],
        roleRightList: [],
        haveRightData: [],
        userTableData: [],
        tableData: [],
        userRightList: [],
        userRightList1: {},
        treeSelectData: {},
        isIcon: {
          icon0: true,
          icon1: true,
          icon2: false,
          icon3: true,
          icon4: false,
          icon5: true,
          icon6: false
        },
        userRowData: {},
        userQueryForm: {
          user_id: '',
          user_name: '',
          deptno: '',
          user_type: '',
          user_pwd: '',
          valid_flag: '',
          user_position: '',
          id_code: '',
          paic_leave_date: '',
          tel: '',
          ext: '',
          mail: '',
          fax: '',
          mobile: '',
          data_state: ''
        }
      };
    },
    methods: {
      /**
       * 机构树选中事件
       * @param  data  选中条目的数据
       * */
      treeSelected (data) {
        this.btnDisable = true;
        this.userRowData = {};
        this.treeSelectData = data[0];
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101205',
            param: {deptno: data[0].deptno}
          },
          success: this.setTableData
        });
      },

      /**
       * 表格获取数据
       * */
      setTableData () {
        let responseData = this.$store.getters.jsonData;
        if (responseData) {
          if (responseData.flag === '0') {
            this.tableData = responseData.results[0].record;
            // TODO 后续加后台返回分页数据
            this.total = this.tableData.length;
            this.userTableData = this.tableData.slice(0, this.pageSize);
          } else {
            this.$uiMessage.error(responseData.prompt);
          }
        }
      },

      /**
       * 遍历树节点
       * @param key：输入框的值
       * @param data：遍历的数据
       * */
      ergodicOrgList (key, data) {
        for (let i = 0; i < data.length; i++) {
          if (data[i].orgname.indexOf(key) > -1) {
            this.userTreeData.push(data[i]);
          } else if (data[i].deptno.indexOf(key) > -1) {
            this.userTreeData.push(data[i]);
          } else if (data[i]['children']) {
            this.ergodicOrgList(key, data[i]['children']);
          }
        }
      },

      /**
       * 表格数据选中事件
       * @param data  表格选中数据
       * */
      userTableRowClick (data) {
        if (this.userRowData.user_id !== data.user_id) {
          this.btnDisable = false;
          this.userRowData = data;
        }
      },

      /**
       * 查询表单确认事件
       * */
      searchModalEvent (param) {
        this.treeSelectData = {};
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101205',
            param: param
          },
          success: this.setTableData
        });
      },

      /**
       * 新增、修改、查看用户
       * @param flag 标识新增、修改
       * */
      queryUser (flag) {
        this.setUser = true;
        this.userForm = true;
        if (flag === 'A') {
          this.formTitle = '新增用户';
          this.itemDisable = false;
          this.$refs['setUserForm'].$refs['queryForm'].resetFields();
          if (this.treeSelectData.deptno !== undefined) {
            this.userQueryForm.deptno = this.treeSelectData.deptno;
          }
        } else if (flag === 'U') {
          this.formTitle = '修改用户';
          this.$refs['setUserForm'].$refs['queryForm'].resetFields();
          this.userQueryForm = copyObject(this.userRowData);
          this.itemDisable = true;
        }
        this.userQueryForm.action = flag;
      },

      /**
       * 新增修改表单取消
       * */
      queryFormCancel () {
        this.$refs['setUserForm'].$refs['queryForm'].resetFields();
        // 收起其他条件
        this.$refs['setUserForm'].isOtherCondition = false;
        this.setUser = false;
        this.userForm = false;
      },

      /**
       * 新增、修改表单数据确认事件
       * */
      queryModalEvent () {
        this.$refs['setUserForm'].$refs['queryForm'].validate((valid) => {
          if (valid) {
            this.userQueryForm.user_pwd = enc(this.userQueryForm.user_pwd);
            this.$store.dispatch('ajaxCallLbm', {
              data: {
                obj_code: this.userQueryForm.action === 'A' ? '101003' : '101004',
                param: this.userQueryForm
              },
              success: () => {
                let responseData = this.$store.getters.jsonData;
                if (responseData.flag === '0') {
                  this.$uiNotice.success({
                    title: '成功',
                    desc: responseData.prompt
                  });
                  this.queryFormCancel();
                  this.btnDisable = true;
                } else {
                  this.$uiNotice.error({
                    title: '失败',
                    desc: responseData.prompt
                  });
                }
                this.treeSelected([this.treeSelectData]);
              }
            });
          }
        });
      },

      /**
       * 页码改变
       * @param page 当前页码
       * */
      pageChange (page) {
        this.userTableData = this.tableData.slice((page - 1) * this.pageSize, page * this.pageSize);
      },

      /**
       * 页数改变事件
       * @param pageSize 当前页面长度
       * */
      pageSizeChange (pageSize) {
        this.pageSize = pageSize;
        this.userTableData = this.tableData.slice(0, pageSize);
      },

      /**
       * 查询机构信息
       */
      getOrgData () {
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '101207',
            param: {}
          },
          success: () => {
            let responseData = this.$store.getters.jsonData;
            if (responseData.flag === '0') {
              let record = responseData.results[0].record;
              this.deptData = record;
              this.userTreeData = [{
                title: '所有部门',
                children: cnvToTree(copyObject(record), '0', 'up_deptno', 'deptno')
              }];
              this.$set(this.userTreeData[0], 'expand', true);
            } else {
              this.$uiNotice.error({
                title: '查询部门信息失败',
                desc: responseData.prompt
              });
            }
          }
        });
      },

      /**
       * 高度自适应
       */
      resize () {
        this.height = Number(document.documentElement.clientHeight);
        let that = this;
        window.onresize = function temp () {
          that.height = Number(document.documentElement.clientHeight);
        };
      }
    },
    computed: {
      styles () {
        return {
          height: `${this.height - 101}px`
        };
      }
    },
    created () {
      // 查询机构信息
      this.getOrgData();
      this.resize();
    }
  };
</script>

<style lang="less" scoped>
  .userManagement {
    width: 100%;
    height: 100%;
    .userRightTabs {
      position: absolute;
      display: block;
      width: 500px;
      z-index: 100;
      top: 60.6px;
      right: 10px;
      bottom: 80.8px;
      background: aliceblue;
    }
  }
</style>



