<template>
  <ui-card>
    <div slot="title" class="handleTable-title">
      <slot name="header"></slot>
    </div>
    <div>
      <ui-table :columns="tableColumns" :data="tableData" :height=tableHeight ref="table" highlight-row
                :row-class-name="rowClassName"
                @on-row-click="tableRowClick"
                @on-selection-change="tableSelectChange"></ui-table>
      <ui-page class="pageClass" :total="total" :page-size="pageSize" show-total
               show-sizer placement="top" :current="current"
               size="small" :page-size-opts=[10,30,50,100] @on-change="pageChange"
               @on-page-size-change="pageSizeChange">
      </ui-page>
    </div>
  </ui-card>
</template>

<script type="text/ecmascript-6">
  export default{
    name: 'handleTable',
    props: {
      loadingData: Boolean, // 是否加载数据
      objcode: String, // 表格查询数据的接口
      tableheight: Number, // 表格高度
      total: Number, // 数据总条数
      current: Number, // 当前页码
      tableColumns: Array, // 表格的列
      tableData: Array, // 表格的数据
      objparam: Object, // 查询时传的参数
      rowClassName: Function // 样式回调方法
    },
    data () {
      return {
        pageSize: 30 // 分页页数
      };
    },
    methods: {
      /**
       * 表格行点击事件
       * @param data 表格点击选中的数据对象obj
       * */
      tableRowClick (data) {
        this.$emit('on-tableRowClick', data);
      },

      /**
       * 表格勾选变化事件
       * @param selection 表格勾选选中数据数组arr
       * */
      tableSelectChange (selection) {
        this.$emit('on-selection-change', selection);
      },

      /**
       * 获取表格数据
       * */
      getTableData () {
        if (this.loadingData) {
          this.$store.dispatch('ajaxCallLbm', {
            data: {
              obj_code: this.objcode,
              param: {},
              param_ext: {record_offset: this.current - 1, record_count: this.pageSize, order_by: ''},
              async: true
            },
            success: this.setTableData
          });
        }
      },

      /**
       * 设置表格数据
       * */
      setTableData () {
        this.$emit('on-setTableData');
      },

      /**
       * 表格改变页码
       * @param page 当前页码int
       */
      pageChange (page) {
        if (this.objcode === '' || this.objcode === undefined) {
          this.$uiNotice.warning({
            title: '请配置正确的接口号'
          });
          return false;
        }
        this.$emit('update:current', page);
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: this.objcode,
            param: this.objparam,
            param_ext: {
              record_offset: this.pageSize * (page - 1),
              record_count: this.pageSize,
              order_by: ''
            },
            async: true
          },
          success: this.setTableData
        });
      },

      /**
       * 每页页数改变
       * @param pageSize 当前页数int
       */
      pageSizeChange (pageSize) {
        if (this.objcode === '' || this.objcode === undefined) {
          this.$uiNotice.warning({
            title: '请配置正确的接口号'
          });
          return false;
        }
        this.$emit('update:current', 1);
        this.pageSize = pageSize;
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: this.objcode,
            param: this.objparam,
            param_ext: {record_offset: this.current - 1, record_count: this.pageSize, order_by: ''},
            async: true
          },
          success: this.setTableData
        });
      }
    },
    computed: {
      tableHeight: function () {
        return Number(this.tableheight - 40.8);
      }
    },
    created () {
      this.getTableData();
    }
  };
</script>

<style lang="less" scoped>
  .handleTable {
    &-title {
      height: 24px;
    }
  }

  /deep/ .ivu-card-head {
    border-bottom: 1px solid #e6e6e6;
    padding: 8px;
    line-height: 1;
  }
</style>



