* 模块名称：公共组件-旧值、新值转换
* 功能说明：展现旧值变为新值的过程
* 作    者：wangqi@szkingdom.com
* 创建日期：20180412
* 版 本 号：1.0
* 修改历史：
* 修改日期                      姓名                    内容
* ---------------------------------------------------------------------------
* ****************************************************************************
-->
<template>
  <div :class="oldtonewClass">
    <ui-row>
      <ui-col span="10">
        <ui-input class="oldtonew-input" :size="size" :readonly=true v-if="comp === 'input'"
                  :value="param.oldvalue"></ui-input>
        <ui-select class="oldtonew-select" v-if="comp === 'select'" :size="size" :value="param.oldvalue"
                   placeholder="">
          <ui-option v-for="item in optionData" :value="item.value" :key="item.value" :disabled=true
                     :label="item.label">
            <span>{{item.label}}</span>
            <span class="optionClass">{{ item.value }}</span>
          </ui-option>
        </ui-select>
        <ui-DatePicker class="oldtonew-datepicker" :value="param.oldvalue"
                       v-if="comp === 'date'" format="yyyy/MM/dd" placeholder=""
                       :readonly=true>
        </ui-DatePicker>
        <ui-time-picker class="oldtonew-timepicker" :value="param.oldvalue" v-if="comp === 'time'"
                        placeholder="" :readonly=true>
        </ui-time-picker>
      </ui-col>
      <ui-col span="4" class="oldtonew-iconCol">
        <ui-icon class="oldtonew-icon" type="right-arrows" color="red"></ui-icon>
      </ui-col>
      <ui-col span="10" class="oldtonew-lastCol">
        <ui-input class="oldtonew-input lastinput" v-if="comp === 'input'" :size="size"
                  :readonly=true :value="param.newvalue"></ui-input>
        <ui-select class="oldtonew-select lastinput" v-if="comp === 'select'" :size="size" :value="param.newvalue"
                   placeholder="">
          <ui-option v-for="item in optionData" :value="item.value" :key="item.value" :disabled=true
                     :label="item.label">
            <span>{{item.label}}</span>
            <span class="optionClass">{{ item.value }}</span>
          </ui-option>
        </ui-select>
        <ui-DatePicker class="oldtonew-datepicker lastinput" :value="param.newvalue"
                       v-if="comp === 'date'" format="yyyy/MM/dd" placeholder=""
                       :readonly=true>
        </ui-DatePicker>
        <ui-time-picker class="oldtonew-timepicker lastinput" :value="param.newvalue" v-if="comp === 'time'"
                        placeholder="" :readonly=true>
        </ui-time-picker>
      </ui-col>
    </ui-row>
  </div>
</template>

<script type="text/ecmascript-6">
  import {oneOf} from '@/assets/js/utils.js';

  export default{
    name: 'uiModifyBox',
    props: {
      optionData: {
        type: Array,
        default: []
      },
      comp: {
        validator (value) {
          return oneOf(value, ['input', 'select', 'date', 'time']);
        }
      },
      size: {
        validator (value) {
          return oneOf(value, ['small', 'large', 'default']);
        }
      },
      param: Object
    },
    data () {
      return {
        testData: [
          {value: '0', label: '测试1'},
          {value: '1', label: '测试2'}
        ]
      };
    },
    computed: {
      oldtonewClass () {
        return [
          'oldtonew-wrapper',
          {
            [`oldtonew-wrapper-${this.size}`]: !!this.size,
            ['oldtonew-type']: this.type
          }
        ];
      }
    }
  };
</script>

<style lang="less" scoped>
  .oldtonew {
    &-wrapper {
      width: 100%;
      /*height: 32px;*/
      border: solid 1px #e6e6e6;
      border-radius: 4px;
    }
    &-iconCol {
      background: oldlace;
      text-align: center;
    }
    &-icon {
      width: 32px;
      height: 32px;
      line-height: 32px;
      font-size: 16px;
      text-align: center;
      z-index: 3;
      font-weight: bold;
    }
    &-wrapper-large &-icon {
      font-size: 18px;
      height: 36px;
      line-height: 36px;
    }
    &-wrapper-small &-icon {
      width: 24px;
      font-size: 14px;
      height: 24px;
      line-height: 24px;
    }

    &-input {
      /deep/ .ivu-input {
        border: none;
        &:focus {
          box-shadow: none;
        }
      }
    }

    &-select {
      /deep/ .ivu-select-selection {
        border: none;
        box-shadow: none;
        &:focus {
          box-shadow: none;
        }
        &:hover {
          box-shadow: none;
        }
      }
    }

    &-datepicker,
    &-timepicker {
      /deep/ .ivu-input {
        border: none;
        &:focus {
          box-shadow: none;
        }
      }
    }
  }

  /deep/ .ivu-select {
    border: none;
    box-shadow: none;
    &:focus {
      box-shadow: none;
    }
  }

  .lastinput {
    /deep/ .ivu-input {
      /*text-align: right;*/
      color: #19be6b;
    }
  }
</style>


