import 'jquery-xml2json';
import CryptoJS from './aes.js';
/**
 * aes加密算法
 * @param  {String}  str
 * @param  {String}  enckey
 */
export function enc(str, enckey) {
  if (str === undefined) return '';
  let keys = ['l', 'm', 'e', 'n', 'z'];
  let key = enckey || (keys[0] + keys[2] + keys[2] + keys[1] + keys[2] + keys[3] + keys[4] + keys[4]
    + keys[0] + keys[2] + keys[2] + keys[1] + keys[2] + keys[3] + keys[4] + keys[4]);
  return _ui_pwd_aes_stringToHex(str, key);
}

function _ui_pwd_aes_stringToHex(str, enckey) {
  let srcs = CryptoJS.enc.Utf8.parse(str);
  let key = CryptoJS.enc.Utf8.parse(enckey);
  let iv = CryptoJS.enc.Utf8.parse('0102020303040506');
  let encrypted = CryptoJS.AES.encrypt(srcs, key, {iv: iv, mode: CryptoJS.mode.CBC});
  return encrypted.toString();

}

/**
 * 判断是否为某种数据类型
 * @param  {String}  type
 * @param  {*}  val
 * @return {Boolean}
 */
export function is(type, val) {
  return Object.prototype.toString.call(val) === `[object ${type}]`;
}

/**
 * 转义xml关键符号 < > " &
 * @param  {String}  type
 * @param  {*}  val
 * @return {Boolean}
 */
export function xmlChar(str) {
  let and = /&/g, less = /</g, greater = />/g, quot = /\"/g;
  return str.replace(and, '&amp;').replace(less, '&lt;').replace(greater, '&gt;').replace(quot, '&quot;');
}

/**
 * 判断指定参数是否是一个纯粹的对象。 调用jquery 的方法 $.isPlainObject
 * @param   obj
 */
export function isPlainObject(obj) {
  return $.isPlainObject(obj);
}

/**
 * 清除字符窜两边的空格
 * @param  {String}  str
 */
export function trim(str) {
  return str.replace(/^\s*/, '').replace(/\s*$/, '');
}

export function xml2json(xml, tag) {
  if (tag) {
    return $.xml2json($(tag, xml)[0])[tag];
  } else {
    return $.xml2json(xml);
  }
}

/**
 * 判断参数是否是其中之一
 * @param value
 * @param validList
 * */
export function oneOf(value, validList) {
  for (let i = 0; i < validList.length; i++) {
    if (value === validList[i]) {
      return true;
    }
  }
  return false;
}

/**
 * 深度拷贝数组  -- 使用jquery的方法
 * @param  {Array}  array
 */
export function copyArray(array) {
  let copy = $.map(array, function (obj) {
    return $.extend(true, {}, obj);// 返回对象的深拷贝
  });
  return copy;
}

/*
 * 日期对象公用方法
 * 常用的格式:
 *  YYYY-MM-DD HH:mm:ss
 */
import moment from 'moment';
export const date = (function () {
  return {
    format (date, dFormat, format) {
      return moment(date, format).format(dFormat);
    }
  };
})();

/*
 * 获取随机数据
 * @param lbound 起始长度
 * @param ubound
 * */
export function getRandomNum(lbound, ubound) {
  return (Math.floor(Math.random() * (ubound - lbound)) + lbound);// 0-char.length长度的
}

/*
 * 前端打包方法与jros一致
 * @param packetParam
 * */
export function packet(packetParam) {
  let param = {};
  param['packet1'] = packetParam.rsName; // 打包结果名称
  param['packet2'] = packetParam.colNum; // 打包列数
  param['packet3'] = packetParam.colName; // 打包列名
  param['packet4'] = packetParam.spliter || ',|'; // 分隔符
  param[packetParam.paramName] = packetParam.paramValue; // 需要打包的参数名及对应的值
  return param;
}

/*
 * 规范化树组件的数据，使其满足树组件数据结构
 * @param srcData 源数据
 * @param rootNode 根节点标识
 * @param pid 父级标识
 * @param id 字段唯一标识
 *
 * author wangqi@szkingdom.com 2017/11/10
 * */
export function cnvToTree(srcData, rootNode, pid, id) {
  let obj = {};
  for (let i = 0; i < srcData.length; i++) {
    if (!obj[srcData[i][pid]]) {
      obj[srcData[i][pid]] = [];
    }
    obj[srcData[i][pid]].push(srcData[i]);
  }
  let _obj = copyObject(obj);
  let idValue = '';
  for (let prop in _obj) {
    for (let j = 0; j < _obj[prop].length; j++) {
      idValue = _obj[prop][j][id];
      if (_obj[idValue]) {
        _obj[prop][j]['children'] = _obj[idValue];
      }
    }
  }
  return _obj[rootNode];
}

/*
 * 复制一个对象
 * @param  obj 原对象
 *
 * author wangqi@szkingdom.com 2017/11/10
 * */
export function copyObject(obj) {
  let newObj = {};
  if (obj instanceof Array) {
    newObj = [];
  }
  for (let key in obj) {
    let val = obj[key];
    newObj[key] = typeof val === 'object' ? copyObject(val) : val;
  }
  return newObj;
}

export function getHeight(val) {
  return window.innerHeight - Number(val);
}



