<template>
  <div class="userManagement-table">
    <ui-card>
      <div slot="title" style="height:24px;">
        <senior-search :visible="searchForm"
                       @on-btnClick="btnClick"
                       @on-reset="reset"
                       @on-cancel="searchForm = false;"
                       @on-ok="searchQuery">
          <ui-form :label-width="120" :model="userSearchForm" ref="searchForm">
            <ui-row>
              <ui-col span="10">
                <ui-form-item prop="user_id" label="用户代码">
                  <ui-input type="text" v-model="userSearchForm.user_id" placeholder="用户代码"
                            :maxlength='16' autofocus></ui-input>
                </ui-form-item>
              </ui-col>
              <ui-col span="10" offset="2">
                <ui-form-item prop="user_name" label="用户名称">
                  <ui-input v-model="userSearchForm.user_name" type="text"
                            placeholder="用户名称"></ui-input>
                </ui-form-item>
              </ui-col>
            </ui-row>
            <ui-row>
              <ui-col span="10">
                <ui-form-item prop="deptno" label="部门编号">
                  <ui-select v-model="userSearchForm.deptno" placeholder="请选择所属部门">
                    <ui-option v-for="item in deptData" :value="item.deptno" :key="deptData.deptno"
                               :label="item.dept_name">
                      <span>{{item.dept_name}}</span>
                      <span style=" float:right;color:#ccc">{{ item.deptno }}</span>
                    </ui-option>
                  </ui-select>
                </ui-form-item>
              </ui-col>
              <ui-col span="10" offset="2">
                <ui-form-item prop="user_type" label="用户类型">
                  <ui-input v-model="userSearchForm.user_type" placeholder="请输入用户类型"></ui-input>
                </ui-form-item>
              </ui-col>
            </ui-row>
            <ui-row>
              <ui-col span="10">
                <ui-form-item prop="valid_flag" label="有效标识">
                  <ui-input v-model="userSearchForm.valid_flag" placeholder="请选择证件类型"></ui-input>
                </ui-form-item>
              </ui-col>
            </ui-row>
          </ui-form>
        </senior-search>
        <div class="split-style"></div>
        <ui-button type="info" size="small" icon="person-add" @click="queryUser('A')">新增</ui-button>
        <ui-button type="info" size="small" icon="edit" :disabled="btnDisable" @click="queryUser('U')">修改</ui-button>
        <span class="card-title-text">用户列表</span>
        <ui-icon type="ios-list-outline" class="card-title-icon"></ui-icon>
      </div>
      <div class="cardBody" :style="cardBodyStyle">
        <ui-table :columns="userTableColumns" :data="userTableData" :height=tableHeight
                  @on-row-click="tableRowClick" highlight-row></ui-table>
        <ui-page class="pageClass" :total="total" :page-size="pageSize" show-total
                 show-sizer placement="top"
                 size="small" :page-size-opts=[10,30,50,100] @on-change="pageChange"
                 @on-page-size-change="pageSizeChange">
        </ui-page>
      </div>
    </ui-card>
  </div>
</template>

<script type="text/ecmascript-6">
  import seniorSearch from '../../commons/seniorSearch.vue';
  import commonDialog from '../../commons/commonDialog.vue';
  import userForm from './userForm.vue';

  let tableWidth = 0;

  export default {
    name: 'userTable_page',
    components: {
      seniorSearch,
      commonDialog,
      userForm
    },
    props: {
      windowsSize: Number,
      total: Number,
      pageSize: Number,
      btnDisable: Boolean, // 控制按钮失效
      userTableData: Array,
      isIcon: Object, // 标识用户状态
      deptData: Array
    },
    data () {
      const pwdVal1 = (rules, value, callback) => {
        if (this.$refs['upwd1'].value !== '') {
          if (this.$refs['upwd1'].value !== value) {
            callback(new Error('密码不一致'));
          }
        }
        callback();
      };

      const _pwdVal1 = (rules, value, callback) => {
        if (this.$refs['upwd'].value !== '') {
          if (this.$refs['upwd'].value !== value) {
            callback(new Error('密码不一致'));
          }
        }
        callback();
      };
      return {
        searchForm: false,
        userPwdModal: false,
        searchKey: '',
        idkindList: [],
        roleUserValue: [],
        statusList: [
          {value: '0', label: '正常用户'},
          {value: '1', label: '冻结用户'},
          {value: '*', label: '注销用户'}
        ],
        userTableColumns: [
          {title: '用户代码', key: 'user_id'},
          {title: '用户名称', key: 'user_name'},
          {title: '部门编号', key: 'deptno'},
          {title: '数据状态', key: 'data_state'},
          {title: '用户类型', key: 'user_type'},
          {title: '有效标识', key: 'valid_flag'},
          {title: '用户职位', key: 'user_position'}
        ],
        userSearchForm: {
          user_id: '',
          user_name: '',
          deptno: '',
          user_type: '',
          valid_flag: ''
        },
        userPwdForm: {
          upwd: '',
          upwd1: ''
        },
        validatePwd: {
          upwd: [
            {required: true, message: '请输入密码', trigger: 'change'},
            {validator: pwdVal1, trigger: 'change'}
          ],
          upwd1: [
            {required: true, message: '请再次输入密码', trigger: 'change'},
            {validator: _pwdVal1, trigger: 'change'}
          ]
        }
      };
    },
    computed: {
      cardBodyStyle() {
        return {
          width: '100%',
          height: `${this.windowsSize - 196}px`,
          overflow: 'auto'
        };
      },
      tableHeight: function () {
        return Number(this.windowsSize - 196 - 40.8);
      },
      userRightStyle() {
        return {
          height: `${this.windowsSize - 196 - 58.8}px`,
          overflow: 'auto'
        };
      }
    },
    methods: {
      /**
       * 表格点击事件
       * @param data 选中的数据
       * */
      tableRowClick(data) {
        this.$emit('on-tableRowClick', data);
      },

      /**
       * 新增、修改用户
       * */
      queryUser(flag) {
        this.$emit('on-queryUser', flag);
      },

      /**
       * 修改用户状态
       * */
      changeUserStatus(flag) {
        this.$emit('on-changeUserStatus', flag);
      },

      /**
       * 查询用户
       * */
      btnClick() {
        this.searchForm = true;
      },

      reset() {
        this.$refs['searchForm'].resetFields();
      },

      /**
       * 查询确认事件
       * */
      searchQuery () {
        this.searchForm = false;
        this.$emit('on-searchModalEvent', this.userSearchForm);
      },

      /**
       * 重置密码
       * */
      setPwd() {
        this.userPwdModal = true;
      },

      /**
       * 重置密码表单确认事件
       * */
      pwdModalEvent () {
        this.$refs['pwdForm'].validate((valid) => {
          if (valid) {
            this.$emit('on-pwdModalEvent', this.userPwdForm);
          } else {
            this.$uiMessage.error('表单验证失败!');
          }
        });
        this.pwdModalCancel();
      },

      /**
       * 重置密码表单取消事件
       * */
      pwdModalCancel() {
        this.$refs['pwdForm'].resetFields();
        this.userPwdModal = false;
      },

      /**
       * 权限操作
       * */
      setUserRight() {
        this.$emit('on-setUserRight');
      },

      /**
       * 页数改变
       * */
      pageChange(page) {
        this.$emit('on-pageChange', page);
      },

      /**
       * 每页显示数据长度改变
       * */
      pageSizeChange(pageSize) {
        this.$emit('on-pageSizeChange', pageSize);
      },

      /**
       * 表格字段宽度
       * @param ratio
       * @param minWidth
       * @returns {number}
       */
      setTableWidth(ratio, minWidth) {
        let colWidth = ratio * tableWidth;
        return colWidth > minWidth ? colWidth : minWidth;
      },

      /**
       * 查询角色数据
       */
      getRoleData() {
        let that = this;
        this.$store.dispatch('ajaxCallLbm', {
          data: {
            obj_code: '',
            param: {}
          },
          success: function () {
            // TODO 需要处理数据，存到data变量中
            let responseData = that.$store.getters.jsonData;
            if (responseData.flag === '0') {
              that.role = responseData.object[0].record;
            } else {
              that.$uiNotice.error({
                title: '查询角色消息权限失败',
                desc: responseData.prompt
              });
            }
          }
        });
      }
    },
    beforeCreate() {
      // 20margin，32是padding，3是border，8是滚动条宽度
      tableWidth = Number(document.documentElement.clientWidth) - 20 - 32 - 3 - 8;
    }
  };
</script>

<style lang="less" scoped>
  .userManagement-table {
    /deep/ .roleSelectClass {
      .ivu-select-selection {
        height: 30px;
        overflow: auto;
      }
    }

    /deep/ .ivu-card-head {
      border-bottom: 1px solid #e6e6e6;
      padding: 8px;
      line-height: 1;
    }
  }
</style>



